// src/utils/dataLoader.js
import Papa from "papaparse";

// Add a simple caching mechanism using sessionStorage
const CACHE_VERSION = "1.1"; // Update this if you change data structure or source significantly
const CACHE_PREFIX = "data-cache-";
const COMPLETE_DATA_CACHE_KEY = `${CACHE_PREFIX}complete-dashboard-data-v${CACHE_VERSION}`;
const VERSION_CACHE_KEY = `${CACHE_PREFIX}version`;

// Unified path configuration
const CSV_PATHS = [
  process.env.PUBLIC_URL + "/data/", // GitHub Pages compatible path
  "/data/", // Local development fallback
  "data/", // Relative path fallback
];

// Enhanced path validator
const validateCSVPath = async (basePath) => {
  try {
    // Use a known critical file for validation
    const testUrl = `${basePath}pqi.csv`; // Assuming pqi.csv is critical and exists
    const response = await fetch(testUrl, { method: "HEAD" }); // Use HEAD request for efficiency

    // Check for valid response (2xx status code)
    if (!response.ok) {
      console.debug(`Path ${basePath} invalid: HTTP ${response.status}`);
      return false;
    }
    console.debug(`Path ${basePath} seems valid.`);
    return true;
  } catch (error) {
    console.debug(`Path ${basePath} validation failed:`, error.message);
    return false;
  }
};

// Check browser support for caching
const isCachingSupported = () => {
  try {
    const testKey = "__testCacheSupport__";
    sessionStorage.setItem(testKey, testKey);
    sessionStorage.removeItem(testKey);
    return true;
  } catch (e) {
    console.warn("SessionStorage caching not supported.");
    return false;
  }
};

// Clear old cache if version changed
const clearCacheIfNeeded = () => {
  if (!isCachingSupported()) return;
  try {
    const storedVersion = sessionStorage.getItem(VERSION_CACHE_KEY);
    if (storedVersion !== CACHE_VERSION) {
      console.log(
        `Cache version mismatch (Stored: ${storedVersion}, Current: ${CACHE_VERSION}). Clearing cache.`
      );
      Object.keys(sessionStorage).forEach((key) => {
        if (key.startsWith(CACHE_PREFIX)) {
          sessionStorage.removeItem(key);
        }
      });
      sessionStorage.setItem(VERSION_CACHE_KEY, CACHE_VERSION);
    }
  } catch (e) {
    console.warn("Error checking/clearing cache version:", e);
  }
};

// Get cached data if available
const getFromCache = (key) => {
  if (!isCachingSupported()) return null;
  try {
    const cachedData = sessionStorage.getItem(key);
    if (!cachedData) return null;
    console.debug(`Cache hit for ${key}`);
    return JSON.parse(cachedData);
  } catch (e) {
    console.warn(`Error reading from cache for key ${key}:`, e);
    // Clear potentially corrupted item
    sessionStorage.removeItem(key);
    return null;
  }
};

// Store data in cache
const storeInCache = (key, data) => {
  if (!isCachingSupported()) return;
  try {
    sessionStorage.setItem(key, JSON.stringify(data));
    console.debug(`Stored data in cache for ${key}`);
  } catch (e) {
    console.warn(`Error storing in cache for key ${key}:`, e);
    // Attempt to clear space if quota exceeded
    if (e.name === "QuotaExceededError") {
      console.warn(
        "Cache quota exceeded. Attempting to clear older cache entries."
      );
      // Implement a more sophisticated cache eviction strategy if needed
      clearCacheIfNeeded(); // Simple clear for now
    }
  }
};

// Core CSV loader with improved error handling and caching
export const loadCSV = async (filePath, basePath) => {
  const fullPath = `${basePath}${filePath}`;
  // Use a more specific cache key including the base path if it varies, though usually it won't
  const cacheKey = `${CACHE_PREFIX}csv-${fullPath.replace(/[^a-z0-9]/gi, "-")}`;
  const cachedData = getFromCache(cacheKey);

  if (cachedData) {
    console.log(`Using cached data for ${filePath}`);
    return cachedData;
  }

  try {
    console.log(`Fetching ${fullPath}...`);
    const startTime = performance.now();
    const response = await fetch(fullPath);

    if (!response.ok) {
      throw new Error(`HTTP ${response.status} - ${response.statusText}`);
    }

    const text = await response.text();
    const contentType = response.headers.get("content-type") || "";

    // Relax content type check slightly, sometimes servers misconfigure CSVs
    if (
      !contentType.includes("text/csv") &&
      !contentType.includes("application/csv") &&
      text.length > 0 &&
      !text.trim().startsWith("<")
    ) {
      console.warn(
        `Unexpected content type for ${fullPath}: ${contentType}. Proceeding with parsing.`
      );
    } else if (
      !contentType.includes("text/csv") &&
      !contentType.includes("application/csv")
    ) {
      throw new Error(`Unexpected content type: ${contentType}`);
    }

    return new Promise((resolve, reject) => {
      Papa.parse(text, {
        header: true,
        dynamicTyping: true, // Automatically convert numbers, booleans
        skipEmptyLines: true,
        complete: (results) => {
          if (results.errors?.length > 0) {
            // Log warnings but don't necessarily fail
            console.warn(`CSV parse warnings for ${fullPath}:`, results.errors);
          }
          if (!results.data) {
            console.warn(
              `CSV file ${fullPath} parsed with null/undefined data.`
            );
            // Resolve with empty array for robustness
            results.data = [];
          }

          const endTime = performance.now();
          console.log(
            `Loaded ${filePath} in ${(endTime - startTime).toFixed(0)}ms with ${
              results.data.length
            } rows`
          );

          // Cache the results
          storeInCache(cacheKey, results.data);

          resolve(results.data);
        },
        error: (error) => {
          console.error(`CSV parse failed for ${fullPath}:`, error);
          reject(new Error(`CSV parse failed: ${error.message}`));
        },
      });
    });
  } catch (error) {
    console.error(`Failed to load or parse ${fullPath}:`, error);
    throw new Error(`Failed to load ${filePath}: ${error.message}`); // Re-throw cleaned error
  }
};

// Main data loader - MODIFIED
// Takes an optional onProgressUpdate callback: (progressUpdate: { stage: string, progress: number }) => void
export const loadAllData = async (onProgressUpdate = () => {}) => {
  console.log("Initializing data load...");
  onProgressUpdate({ stage: "Checking cache...", progress: 5 }); // Initial progress update

  clearCacheIfNeeded(); // Check cache version consistency

  // Check if we have a complete data set cached
  const cachedFullData = getFromCache(COMPLETE_DATA_CACHE_KEY);
  if (cachedFullData) {
    console.log("Using complete cached data set.");
    onProgressUpdate({ stage: "Loaded from cache", progress: 100 });
    await new Promise((resolve) => setTimeout(resolve, 50)); // Brief delay to show message
    return cachedFullData;
  }
  console.log("No complete cache found. Proceeding with fetch.");
  onProgressUpdate({ stage: "Validating paths...", progress: 10 });

  // Find valid base path
  let basePath = null;
  for (const path of CSV_PATHS) {
    if (await validateCSVPath(path)) {
      basePath = path;
      break;
    }
  }

  if (!basePath) {
    const errorMsg =
      "CSV file path validation failed. Cannot find data directory.";
    console.error(errorMsg);
    // Update progress before throwing
    onProgressUpdate({
      stage: "Error: Cannot find data directory.",
      progress: 0,
    });
    throw new Error(errorMsg);
  }

  console.log(`Using base path: ${basePath}`);
  onProgressUpdate({ stage: "Loading data files...", progress: 20 });

  // Define all data files required by the dashboard
  const dataFiles = [
    // Define files and mark critical ones
    { key: "pqiData", file: "pqi.csv", critical: true },
    { key: "cqiData", file: "cqi.csv", critical: true },
    { key: "primesSplineData", file: "primes_splines.csv", critical: true },
    { key: "fullData", file: "full_data.csv", critical: false }, // Often large
    { key: "primesRawData", file: "primes_raw.csv", critical: false },
    {
      key: "playerPredictions",
      file: "player_predictions.csv",
      critical: false,
    },
    // Add other files if needed
  ];

  const data = {};
  let filesAttempted = 0;
  const totalFiles = dataFiles.length;

  // Estimate progress per file
  const progressIncrement = (100 - 20) / totalFiles; // Progress from 20% to 100% for loading phase

  try {
    // Load files in parallel using Promise.all
    const loadPromises = dataFiles.map(async (fileInfo) => {
      try {
        const fileData = await loadCSV(fileInfo.file, basePath);
        data[fileInfo.key] = fileData; // Assign loaded data
      } catch (error) {
        // Decide how to handle failure based on criticality
        if (fileInfo.critical) {
          console.error(
            `CRITICAL FAILURE: Could not load ${fileInfo.file}. Aborting.`
          );
          // Re-throw the error to make Promise.all fail
          throw new Error(
            `Failed to load critical file: ${fileInfo.file}. ${error.message}`
          );
        } else {
          console.warn(
            `Failed to load non-critical file: ${fileInfo.file}. Setting to empty array. Error: ${error.message}`
          );
          data[fileInfo.key] = []; // Assign empty array on non-critical failure
        }
      } finally {
        // Update progress regardless of success or failure for this file
        filesAttempted++;
        const currentProgress =
          20 + Math.round(filesAttempted * progressIncrement);
        onProgressUpdate({
          stage: `Loading data files (${filesAttempted}/${totalFiles})...`,
          // Cap progress slightly before final steps
          progress: Math.min(currentProgress, 95),
        });
      }
    });

    // Wait for all load attempts (promises) to settle
    await Promise.all(loadPromises);

    // If we reach here, all critical files loaded successfully (or errors were handled)
    console.log("All data load attempts complete. Final processing...");
    onProgressUpdate({
      stage: "Processing complete, caching...",
      progress: 98,
    });

    // Final check for critical data completeness (should be redundant if throw worked)
    let dataComplete = true;
    Object.entries(data).forEach(([key, value]) => {
      const fileInfo = dataFiles.find((f) => f.key === key);
      if ((!value || value.length === 0) && fileInfo?.critical) {
        console.error(`- ${key}: CRITICAL DATA MISSING OR EMPTY after load.`);
        dataComplete = false;
      }
    });

    if (!dataComplete) {
      // This indicates a logic issue if Promise.all didn't reject
      throw new Error(
        "Critical data files are missing or empty after loading completed."
      );
    }

    // Store the complete data set in cache *only if all critical files loaded*
    storeInCache(COMPLETE_DATA_CACHE_KEY, data);
    console.log("All required data loaded and cached.");
    onProgressUpdate({ stage: "Complete", progress: 100 }); // Final progress update

    return data; // Return the fully loaded data object
  } catch (error) {
    // Catch errors from Promise.all (critical file load failure) or final check
    console.error("Data load process failed:", error);
    // Update progress to show error state before re-throwing
    onProgressUpdate({ stage: `Error: ${error.message}`, progress: 0 });
    throw error; // Re-throw the error so the calling component (App.js) knows loading failed
  }
};
