// src/utils/api.js

// Define the API endpoint URL (consider making this an environment variable later)
const API_BASE_URL =
  "https://peakperformr-api-final.yellowcliff-d82a2703.westus2.azurecontainerapps.io";

/**
 * Fetches data from the prime recalculation API.
 *
 * @param {object} apiConfig - Configuration object containing endpoint and params.
 * @param {string} apiConfig.endpoint - The specific API path (e.g., '/recalculate').
 * @param {object} apiConfig.params - The parameters to send in the request body
 *                                    (e.g., { thresholdPct, gamesPctThreshold }).
 * @returns {Promise<object>} A promise that resolves with the parsed JSON response from the API.
 * @throws {Error} If the network request fails or the API returns an error status.
 */
export const fetchDataFromApi = async (apiConfig) => {
  if (!apiConfig || !apiConfig.endpoint || !apiConfig.params) {
    throw new Error("Invalid API configuration provided to fetchDataFromApi.");
  }

  const fullUrl = `${API_BASE_URL}${apiConfig.endpoint}`;
  const params = apiConfig.params;

  console.log(`Making API call to ${fullUrl} with params:`, params);

  try {
    // Start a timer for performance tracking
    const startTime = performance.now();

    const response = await fetch(fullUrl, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        // Add any other necessary headers like API keys if required
        // 'Authorization': `Bearer YOUR_API_KEY`
      },
      body: JSON.stringify(params), // Send parameters as JSON body
    });

    // Calculate time taken for API call
    const apiCallTime = ((performance.now() - startTime) / 1000).toFixed(1);
    console.log(`API response received in ${apiCallTime} seconds`);

    // Check if the response status code indicates success (e.g., 200-299)
    if (!response.ok) {
      // Attempt to read error details from the response body
      let errorBody = "No error details provided.";
      try {
        // Try parsing as JSON first
        const errorJson = await response.json();
        errorBody =
          errorJson.message || errorJson.detail || JSON.stringify(errorJson);
      } catch (jsonError) {
        // If not JSON, try as text
        try {
          errorBody = await response.text();
        } catch (textError) {
          // Ignore if text also fails
        }
      }
      // Throw a detailed error
      throw new Error(
        `API request failed with status ${response.status} (${response.statusText}). Server response: ${errorBody}`
      );
    }

    // Start timer for JSON parsing
    const parseStartTime = performance.now();

    // Parse the successful JSON response
    const data = await response.json();

    // Track parsing time
    const parseTime = ((performance.now() - parseStartTime) / 1000).toFixed(1);

    // Log summary statistics about the API response
    console.log(`API Response parsed in ${parseTime} seconds`);

    if (data) {
      const dataStats = {
        fullData: data.fullData?.length || 0,
        pqi: data.pqi?.length || 0,
        cqi: data.cqi?.length || 0,
        rawPrimes: data.rawPrimes?.length || 0,
        splinePrimes: data.splinePrimes?.length || 0,
        playerPredictions: data.playerPredictions?.length || 0,
      };

      console.log("API Response statistics:", dataStats);

      // Check for boolean flag format in the data to help with debugging
      if (data.fullData && data.fullData.length > 0) {
        const samplePlayer = data.fullData[0];
        console.log("Sample fullData record:", {
          id: samplePlayer.id,
          in_prime: samplePlayer.in_prime,
          in_prime_type: typeof samplePlayer.in_prime,
          is_peak_age: samplePlayer.is_peak_age,
          is_peak_age_type: typeof samplePlayer.is_peak_age,
        });

        // Count in_prime values to verify data integrity
        const primeCount = data.fullData.filter(
          (p) =>
            p.in_prime === true || p.in_prime === "true" || p.in_prime === 1
        ).length;

        const peakCount = data.fullData.filter(
          (p) =>
            p.is_peak_age === true ||
            p.is_peak_age === "true" ||
            p.is_peak_age === 1
        ).length;

        console.log(
          `Data integrity check: ${primeCount} in_prime records, ${peakCount} is_peak_age records out of ${data.fullData.length} total`
        );
      }
    }

    return data; // Resolve the promise with the data
  } catch (error) {
    console.error("Error during API call:", error);
    // Re-throw the error so the calling component (Dashboard) can handle it
    throw error;
  }
};

/**
 * Performs pre-validation on API data before full processing
 * @param {object} data - Raw API response data
 * @returns {boolean} Whether the data passed initial validation
 */
export const validateApiDataFormat = (data) => {
  if (!data) {
    console.error("API returned null or undefined data");
    return false;
  }

  // Check required data structures
  const requiredArrays = [
    "fullData",
    "pqi",
    "cqi",
    "rawPrimes",
    "splinePrimes",
  ];

  for (const arrayName of requiredArrays) {
    if (!Array.isArray(data[arrayName])) {
      console.error(`API data missing required array: ${arrayName}`);
      return false;
    }

    if (data[arrayName].length === 0) {
      console.error(`API returned empty array for: ${arrayName}`);
      return false;
    }
  }

  // Check if fullData has required field structures
  if (data.fullData.length > 0) {
    const sampleRecord = data.fullData[0];
    const requiredFields = ["id", "player_name", "age"];

    for (const field of requiredFields) {
      if (sampleRecord[field] === undefined) {
        console.error(`API fullData missing required field: ${field}`);
        return false;
      }
    }
  }

  return true;
};

