// src/components/PositionDisplay.js
import React from "react";

/**
 * Component for displaying standardized, human-readable position names
 *
 * @param {Object} props
 * @param {string} props.position - Position code (e.g., "PG", "C", "1B")
 * @param {string} props.league - League code for context-specific mapping
 * @param {string} [props.sport] - Optional sport for additional context
 * @param {boolean} [props.showFull=true] - Whether to show full position name
 * @param {string} [props.size="medium"] - Size variant: "small", "medium", "large"
 * @param {string} [props.className] - Additional CSS class names
 */
const PositionDisplay = ({
  position,
  league,
  sport,
  showFull = true,
  size = "medium",
  className = "",
}) => {
  if (!position) return <span className="position-display-empty">--</span>;

  // Get full position name from our mappings
  const fullPositionName = getFullPositionName(position, league, sport);

  // Display either full position name or original abbreviation
  const displayText = showFull ? fullPositionName : position;

  return (
    <span
      className={`position-display ${size} ${className}`}
      title={fullPositionName}
    >
      {displayText}
    </span>
  );
};

/**
 * Gets the full position name based on the position code and league/sport context
 */
const getFullPositionName = (position, league, sport) => {
  // Try to find a mapping based on league first
  const leagueMapping = positionsByLeague[league];
  if (leagueMapping && leagueMapping[position]) {
    return leagueMapping[position];
  }

  // Try sport-specific mapping if available
  if (sport) {
    const sportMapping = positionsBySport[sport];
    if (sportMapping && sportMapping[position]) {
      return sportMapping[position];
    }
  }

  // If no mapping found, just return the original position
  return position;
};

