// src/components/PrimeDistribution.js
import React, { useState, useEffect, useMemo, useCallback } from "react";
import {
  ResponsiveContainer,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  Line,
  ComposedChart,
  Area,
  ReferenceLine,
  Bar,
  Label,
  ScatterChart,
  Scatter,
  Cell,
  Text,
} from "recharts";
import { getPrimeDistributionByAge } from "../utils/dataProcessing"; // Using improved version
import { leagueColorMapping } from "../utils/constants";
import EnhancedFilters from "./EnhancedFilters";
import ContextInfo from "./ContextInfo";
import LeagueDisplay from "./LeagueDisplay";
import SportDisplay from "./SportDisplay";
import PositionDisplay from "./PositionDisplay";
import _ from "lodash";
import "../styles/PrimeDistribution.css";
import "../styles/EnhancedFilters.css";

// Add a new summary component
const PrimeDistributionSummary = ({ primeDistribution, averages }) => {
  if (!primeDistribution || primeDistribution.length === 0) return null;

  // Find age with highest % In Prime
  const peakPrimeData = _.maxBy(primeDistribution, "primePercentage");
  const peakPrimeAge = peakPrimeData?.age ?? "N/A";
  const peakPrimePercentage =
    typeof peakPrimeData?.primePercentage === "number"
      ? peakPrimeData.primePercentage.toFixed(1)
      : "N/A";

  // Find age with highest % At Peak
  const peakAgeDataPoint = _.maxBy(primeDistribution, "peakPercentage");
  const peakAgeDistribution = peakAgeDataPoint?.age ?? "N/A";
  const peakAgePercentage =
    typeof peakAgeDataPoint?.peakPercentage === "number"
      ? peakAgeDataPoint.peakPercentage.toFixed(1)
      : "N/A";

  // Use averages calculated from Spline data if available
  const avgAgeInPrime =
    typeof averages?.start === "number" && typeof averages?.end === "number"
      ? ((averages.start + averages.end) / 2).toFixed(1)
      : "N/A";
  const avgPeakAge =
    typeof averages?.peak === "number" ? averages.peak.toFixed(1) : "N/A";

  // Calculate total sample size (sum of totalPlayers at each age)
  const totalSampleSize = _.sumBy(primeDistribution, "totalPlayers") || 0;

  return (
    <div className="prime-distribution-summary">
      <h3>
        Prime Distribution Summary
        <ContextInfo
          title="Prime Distribution Summary"
          description="This section provides key statistics derived from the charts below. It includes the age with the highest percentage of athletes identified as being 'In Prime', the most common 'Peak Age' observed, the weighted average age during prime, the average peak age based on spline data, and the total sample size analyzed."
        />
      </h3>
      <div className="summary-grid">
        <div className="summary-card">
          <div className="summary-value">{peakPrimeAge}</div>
          <div className="summary-label">Age with Highest % In Prime</div>
          <div className="summary-subtext">
            {peakPrimePercentage !== "N/A"
              ? `${peakPrimePercentage}% of athletes`
              : ""}
          </div>
        </div>

        <div className="summary-card">
          <div className="summary-value">{peakAgeDistribution}</div>
          <div className="summary-label">Most Common Peak Age (%)</div>
          <div className="summary-subtext">
            {peakAgePercentage !== "N/A"
              ? `${peakAgePercentage}% of athletes`
              : ""}
          </div>
        </div>

        <div className="summary-card">
          {/* Use averages calculated from Spline data if available */}
          <div className="summary-value">
            {averages?.start?.toFixed(1) ?? "N/A"}
          </div>
          <div className="summary-label">Average Start Age</div>
          <div className="summary-subtext"></div>
        </div>

        <div className="summary-card">
          <div className="summary-value">{avgPeakAge}</div>
          <div className="summary-label">Average Peak Age</div>
          <div className="summary-subtext"></div>
        </div>

        <div className="summary-card">
          {/* Use averages calculated from Spline data if available */}
          <div className="summary-value">
            {averages?.end?.toFixed(1) ?? "N/A"}
          </div>
          <div className="summary-label">Average End Age</div>
          <div className="summary-subtext"></div>
        </div>

        {/* Keep Total Sample Size */}
        <div className="summary-card">
          <div className="summary-value">
            {totalSampleSize > 0 ? totalSampleSize.toLocaleString() : "N/A"}
          </div>
          <div className="summary-label">Total Sample Size</div>
          <div className="summary-subtext">Data points analyzed</div>
        </div>
      </div>
    </div>
  );
};

// --- Helper Function to Safely Get Data for Filtering ---
const getSafeFilteredData = (
  data,
  key,
  selectedSports,
  selectedLeagues,
  selectedPositions
) => {
  if (!data || !Array.isArray(data[key])) {
    console.warn(
      `getSafeFilteredData: Invalid or missing data for key "${key}".`
    );
    return [];
  }

  let filtered = [...data[key]]; // Work on a copy

  try {
    if (selectedSports && selectedSports.length > 0) {
      filtered = filtered.filter(
        (item) => item?.sport && selectedSports.includes(item.sport)
      );
    }
    if (selectedLeagues && selectedLeagues.length > 0) {
      filtered = filtered.filter(
        (item) => item?.league && selectedLeagues.includes(item.league)
      );
    }
    if (selectedPositions && selectedPositions.length > 0) {
      filtered = filtered.filter(
        (item) => item?.position && selectedPositions.includes(item.position)
      );
    }
  } catch (error) {
    console.error(`Error during filtering for key "${key}":`, error);
    return filtered;
  }
  return filtered;
};

const PrimeDistribution = ({
  data,
  leagues,
  selectedSports = [],
  selectedLeagues = [],
  selectedPositions = [],
  setSelectedSports = () => {},
  setSelectedLeagues = () => {},
  setSelectedPositions = () => {},
  useLogos = false,
}) => {
  const [primeDistribution, setPrimeDistribution] = useState([]);
  const [combinedAgeData, setCombinedAgeData] = useState([]);
  const [averages, setAverages] = useState({});
  const [leagueChars, setLeagueChars] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [dataSource, setDataSource] = useState(""); // For debugging

  // Get all unique sports and positions from the base PQI data
  const allSports = useMemo(() => {
    if (!data?.pqiData) return [];
    return [...new Set(data.pqiData.map((player) => player.sport))].filter(
      Boolean
    );
  }, [data?.pqiData]);

  const allPositions = useMemo(() => {
    if (!data?.pqiData) return [];
    return [...new Set(data.pqiData.map((player) => player.position))].filter(
      Boolean
    );
  }, [data?.pqiData]);

  // Data processing logic encapsulated
  const processAndSetData = useCallback(
    (currentData) => {
      setIsLoading(true);

      setTimeout(() => {
        if (
          !currentData ||
          !currentData.fullData ||
          !currentData.primesSplineData
        ) {
          console.warn(
            "PrimeDistribution: Missing required data for processing (fullData, primesSplineData).",
            currentData
          );
          setPrimeDistribution([]);
          setCombinedAgeData([]);
          setAverages({});
          setLeagueChars([]);
          setIsLoading(false);
          setDataSource("");
          return;
        }

        try {
          // Set data source for debugging
          setDataSource(currentData.definition_key || "unknown");

          // Apply filters safely
          const filteredFullData = getSafeFilteredData(
            currentData,
            "fullData",
            selectedSports,
            selectedLeagues,
            selectedPositions
          );
          const filteredPrimesData = getSafeFilteredData(
            currentData,
            "primesSplineData",
            selectedSports,
            selectedLeagues,
            selectedPositions
          );

          // Log data counts for debugging
          console.log(
            `PrimeDistribution: Processing ${filteredFullData.length} fullData records and ${filteredPrimesData.length} primesSplineData records`
          );

          // Validate fullData to ensure it has proper prime indicators
          const primeCount = filteredFullData.filter(
            (p) => p.in_prime === true
          ).length;
          const peakCount = filteredFullData.filter(
            (p) => p.is_peak_age === true
          ).length;
          console.log(
            `PrimeDistribution: fullData contains ${primeCount} in-prime and ${peakCount} peak-age records`
          );

          // Process main distribution data using filtered full data
          const distribution = getPrimeDistributionByAge(filteredFullData);
          setPrimeDistribution(distribution);
          console.log(
            `PrimeDistribution: Generated ${distribution.length} distribution data points`
          );

          // Process combined age data (bars) using filtered primesSplineData
          const { startAgeData, peakAgeData, endAgeData } =
            calculateAgeCounts(filteredPrimesData);
          const combined = combineAgeData(
            startAgeData,
            peakAgeData,
            endAgeData
          );
          setCombinedAgeData(combined);
          console.log(
            `PrimeDistribution: Generated ${combined.length} combined age data points`
          );

          // Calculate averages using filtered primesSplineData
          if (filteredPrimesData.length > 0) {
            const avgStart = _.meanBy(
              filteredPrimesData.filter((p) => typeof p.start_age === "number"),
              "start_age"
            );
            const avgPeak = _.meanBy(
              filteredPrimesData.filter(
                (p) => typeof p.max_value_age === "number"
              ),
              "max_value_age"
            );
            const avgEnd = _.meanBy(
              filteredPrimesData.filter((p) => typeof p.end_age === "number"),
              "end_age"
            );

            const avgObj = {
              start: !isNaN(avgStart) ? avgStart : null,
              peak: !isNaN(avgPeak) ? avgPeak : null,
              end: !isNaN(avgEnd) ? avgEnd : null,
            };

            setAverages(avgObj);
            console.log("PrimeDistribution: Calculated averages:", avgObj);
          } else {
            setAverages({});
          }

          // Calculate league characteristics using filtered primesSplineData
          const chars = calculateLeagueChars(filteredPrimesData);
          setLeagueChars(chars);
          console.log(
            `PrimeDistribution: Generated ${chars.length} league characteristics`
          );
        } catch (error) {
          console.error(
            "Error during Prime Distribution data processing:",
            error
          );
          // Reset state on error
          setPrimeDistribution([]);
          setCombinedAgeData([]);
          setAverages({});
          setLeagueChars([]);
        } finally {
          setIsLoading(false);
        }
      }, 0);
    },
    [selectedSports, selectedLeagues, selectedPositions]
  );

  // Effect to run processing when data or filters change
  useEffect(() => {
    console.log(
      "PrimeDistribution: Data or filters changed, processing data..."
    );
    processAndSetData(data);
  }, [data, processAndSetData]);

  // --- Data processing sub-functions ---

  const calculateAgeCounts = (primesData) => {
    if (!primesData || !Array.isArray(primesData) || primesData.length === 0) {
      return { startAgeData: [], peakAgeData: [], endAgeData: [] };
    }
    const counts = { start: {}, peak: {}, end: {} };

    primesData.forEach((player) => {
      // Check type before incrementing
      if (typeof player?.start_age === "number") {
        counts.start[player.start_age] =
          (counts.start[player.start_age] || 0) + 1;
      }
      if (typeof player?.max_value_age === "number") {
        counts.peak[player.max_value_age] =
          (counts.peak[player.max_value_age] || 0) + 1;
      }
      if (typeof player?.end_age === "number") {
        counts.end[player.end_age] = (counts.end[player.end_age] || 0) + 1;
      }
    });

    return {
      startAgeData: processAgeData(counts.start),
      peakAgeData: processAgeData(counts.peak),
      endAgeData: processAgeData(counts.end),
    };
  };

  const processAgeData = (counts) => {
    return Object.entries(counts)
      .map(([age, count]) => ({ age: parseInt(age), count }))
      .filter((item) => !isNaN(item.age))
      .sort((a, b) => a.age - b.age);
  };

  const combineAgeData = (start, peak, end) => {
    if (!start || !peak || !end) return [];

    const allAges = new Set([
      ...start.map((d) => d.age),
      ...peak.map((d) => d.age),
      ...end.map((d) => d.age),
    ]);

    if (allAges.size === 0) return [];

    const minAge = Math.min(...allAges);
    const maxAge = Math.max(...allAges);

    if (!isFinite(minAge) || !isFinite(maxAge)) return [];

    return Array.from({ length: maxAge - minAge + 1 }, (_, i) => {
      const age = minAge + i;
      return {
        age,
        startCount: start.find((d) => d.age === age)?.count || 0,
        peakCount: peak.find((d) => d.age === age)?.count || 0,
        endCount: end.find((d) => d.age === age)?.count || 0,
      };
    });
  };

  const calculateTrendLines = (data, key, smoothingFactor = 0.2) => {
    if (!data || data.length < 2) return []; // Need at least 2 points for trend

    const sortedData = [...data].sort((a, b) => a.age - b.age);
    const result = [];

    // Find first valid value
    let smoothed = null;
    for (let i = 0; i < sortedData.length; i++) {
      const val = sortedData[i][key];
      if (typeof val === "number" && !isNaN(val)) {
        smoothed = val;
        break;
      }
    }

    // If no valid values found, return empty array
    if (smoothed === null) return [];

    for (let i = 0; i < sortedData.length; i++) {
      const currentValue = sortedData[i][key];
      // Only update smoothed value if currentValue is a valid number
      if (typeof currentValue === "number" && !isNaN(currentValue)) {
        smoothed =
          smoothingFactor * currentValue + (1 - smoothingFactor) * smoothed;
      }
      // Always push a point, using the last valid smoothed value if current is invalid
      result.push({ age: sortedData[i].age, value: smoothed });
    }
    return result;
  };

  const calculateLeagueChars = (filteredPrimesData) => {
    if (!filteredPrimesData || filteredPrimesData.length === 0) return [];

    const leagueGroups = _.groupBy(filteredPrimesData, "league");

    const leagueCharsData = Object.entries(leagueGroups)
      .map(([league, group]) => {
        if (!league || group.length < 5) return null; // Skip leagues with few players

        const validStartAges = group.filter(
          (p) => typeof p.start_age === "number"
        );
        const validPeakAges = group.filter(
          (p) => typeof p.max_value_age === "number"
        );
        const validEndAges = group.filter((p) => typeof p.end_age === "number");

        // Skip if insufficient data
        if (
          validStartAges.length < 5 ||
          validPeakAges.length < 5 ||
          validEndAges.length < 5
        ) {
          return null;
        }

        const avgStartAge = _.meanBy(validStartAges, "start_age");
        const avgPeakAge = _.meanBy(validPeakAges, "max_value_age");
        const avgEndAge = _.meanBy(validEndAges, "end_age");

        // Add career start/end age (typically around 18 and 35)
        const careerStartAge = Math.max(18, avgStartAge - 2);
        const careerEndAge = Math.min(40, avgEndAge + 2);

        const sport = group.length > 0 ? group[0].sport : "";

        if (
          !isNaN(avgStartAge) &&
          !isNaN(avgPeakAge) &&
          !isNaN(avgEndAge) &&
          leagueColorMapping[league]
        ) {
          return {
            league,
            sport,
            playerCount: group.length,
            avgStartAge: Number(avgStartAge.toFixed(1)),
            avgPeakAge: Number(avgPeakAge.toFixed(1)),
            avgEndAge: Number(avgEndAge.toFixed(1)),
            careerStartAge: Number(careerStartAge.toFixed(1)),
            careerEndAge: Number(careerEndAge.toFixed(1)),
          };
        }
        return null;
      })
      .filter((item) => item !== null)
      .sort((a, b) => b.avgPeakAge - a.avgPeakAge);

    return leagueCharsData;
  };

  // --- RENDER FUNCTIONS ---

  const renderPrimePhaseDistribution = () => {
    // Use combinedAgeData for bars, calculate trends from it
    if (!combinedAgeData || combinedAgeData.length === 0) {
      return (
        <div className="no-data">
          No prime phase distribution data available for visualization.
        </div>
      );
    }

    const validData = combinedAgeData.filter(
      (d) =>
        d.age >= 18 &&
        d.age <= 40 &&
        (d.startCount > 0 || d.peakCount > 0 || d.endCount > 0)
    );

    if (validData.length === 0) {
      return (
        <div className="no-data">
          No valid prime phase data in age range 18-40.
        </div>
      );
    }

    // Calculate trends based on the valid bar data
    const startTrend = calculateTrendLines(validData, "startCount", 0.2);
    const peakTrend = calculateTrendLines(validData, "peakCount", 0.2);
    const endTrend = calculateTrendLines(validData, "endCount", 0.2);

    const maxCount = Math.max(
      ...validData.map((d) => Math.max(d.startCount, d.peakCount, d.endCount)),
      0
    );
    const yAxisMax = Math.max(10, Math.ceil(maxCount / 10) * 10); // Adjust y-axis scale based on counts

    // Custom tooltip component for prime phase distribution
    const CustomTooltipContent = ({ active, payload, label }) => {
      if (!active || !payload || !payload.length || label == null) return null;

      // Find the data point for this age
      const pointData = validData.find((d) => d.age === label);
      if (!pointData) return null;

      return (
        <div
          className="custom-tooltip"
          style={{
            backgroundColor: "white",
            padding: "10px",
            border: "1px solid #ccc",
            borderRadius: "4px",
            boxShadow: "2px 2px 5px rgba(0,0,0,0.1)",
          }}
        >
          <p
            style={{
              margin: "0 0 5px",
              fontWeight: "bold",
              borderBottom: "1px solid #eee",
              paddingBottom: "5px",
            }}
          >
            Age: {label}
          </p>
          {/* Display Bar Counts */}
          <p style={{ margin: "3px 0", color: "#8884d8" }}>
            Prime Starts: {pointData.startCount}
          </p>
          <p style={{ margin: "3px 0", color: "#82ca9d" }}>
            Peak Ages: {pointData.peakCount}
          </p>
          <p style={{ margin: "3px 0", color: "#ffc658" }}>
            Prime Ends: {pointData.endCount}
          </p>
        </div>
      );
    };

    return (
      <div className="chart-container">
        <h3>
          Prime Phase Distribution
          <ContextInfo
            title="Prime Phase Distribution"
            description="This chart shows the distribution of prime starts, peaks, and ends across different ages based on spline data. The bars represent the actual number of athletes whose prime started, peaked, or ended at that specific age. The smooth curves represent statistical trends."
          />
        </h3>
        <ResponsiveContainer width="100%" height={500}>
          <ComposedChart
            data={validData}
            margin={{ top: 40, right: 30, left: 30, bottom: 60 }}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis
              dataKey="age"
              type="number"
              domain={[18, 40]}
              allowDataOverflow={false}
              label={{
                value: "Age",
                position: "insideBottom",
                offset: -15,
                style: { textAnchor: "middle" },
              }}
              ticks={[20, 22, 24, 26, 28, 30, 32, 34, 36, 38, 40]}
            />
            <YAxis
              domain={[0, yAxisMax]}
              tickFormatter={(value) => value.toLocaleString()}
              label={{
                value: "Player Count",
                angle: -90,
                position: "insideLeft",
                style: { textAnchor: "middle" },
                dx: -10,
              }}
              allowDecimals={false}
            />
            {/* Use the custom tooltip content */}
            <Tooltip content={<CustomTooltipContent />} />
            <Legend
              layout="horizontal"
              align="center"
              verticalAlign="top"
              wrapperStyle={{ paddingBottom: 10 }}
            />

            <Bar
              dataKey="startCount"
              fill="#8884d8"
              opacity={0.7}
              name="Prime Starts"
              barSize={8}
            />
            <Bar
              dataKey="peakCount"
              fill="#82ca9d"
              opacity={0.7}
              name="Peak Ages"
              barSize={8}
            />
            <Bar
              dataKey="endCount"
              fill="#ffc658"
              opacity={0.7}
              name="Prime Ends"
              barSize={8}
            />

            {/* Only render trend lines if data exists */}
            {startTrend.length > 0 && (
              <Line
                data={startTrend}
                type="monotone"
                dataKey="value"
                stroke="#413ea0"
                strokeWidth={3}
                name="Start Trend"
                dot={false}
                activeDot={false}
              />
            )}
            {peakTrend.length > 0 && (
              <Line
                data={peakTrend}
                type="monotone"
                dataKey="value"
                stroke="#1f7a4d"
                strokeWidth={3}
                name="Peak Trend"
                dot={false}
                activeDot={false}
              />
            )}
            {endTrend.length > 0 && (
              <Line
                data={endTrend}
                type="monotone"
                dataKey="value"
                stroke="#cc8b00"
                strokeWidth={3}
                name="End Trend"
                dot={false}
                activeDot={false}
              />
            )}

            {/* Reference Lines - ensure averages are numbers */}
            {typeof averages.start === "number" && (
              <ReferenceLine
                x={averages.start}
                stroke="#413ea0"
                strokeDasharray="3 3"
                strokeWidth={2}
              >
                <Label
                  value={`Avg Start: ${averages.start.toFixed(1)}`}
                  position="bottom"
                  fill="#413ea0"
                  fontSize={12}
                  dy={20}
                />
              </ReferenceLine>
            )}
            {typeof averages.peak === "number" && (
              <ReferenceLine
                x={averages.peak}
                stroke="#1f7a4d"
                strokeDasharray="3 3"
                strokeWidth={2}
              >
                <Label
                  value={`Avg Peak: ${averages.peak.toFixed(1)}`}
                  position="bottom"
                  fill="#1f7a4d"
                  fontSize={12}
                  dy={35}
                />
              </ReferenceLine>
            )}
            {typeof averages.end === "number" && (
              <ReferenceLine
                x={averages.end}
                stroke="#cc8b00"
                strokeDasharray="3 3"
                strokeWidth={2}
              >
                <Label
                  value={`Avg End: ${averages.end.toFixed(1)}`}
                  position="bottom"
                  fill="#cc8b00"
                  fontSize={12}
                  dy={50}
                />
              </ReferenceLine>
            )}
          </ComposedChart>
        </ResponsiveContainer>

        <div className="phase-summary-box">
          {/* Use averages passed to component */}
          <h4>Key Insights </h4>
          <ul>
            <li>
              <strong>Avg Prime Start Age:</strong>{" "}
              {averages?.start?.toFixed(1) ?? "N/A"}
            </li>
            <li>
              <strong>Avg Peak Performance Age:</strong>{" "}
              {averages?.peak?.toFixed(1) ?? "N/A"}
            </li>
            <li>
              <strong>Avg Prime End Age:</strong>{" "}
              {averages?.end?.toFixed(1) ?? "N/A"}
            </li>
            <li>
              <strong>Avg Prime Duration:</strong>{" "}
              {averages?.start && averages?.end
                ? (averages.end - averages.start + 1).toFixed(1)
                : "N/A"}{" "}
              years
            </li>
          </ul>
        </div>
      </div>
    );
  };

  // Updated to use the provided code snippet
  const renderLeaguePrimeCharacteristics = () => {
    if (leagueChars.length === 0) return null;

    return (
      <div className="chart-container full-width">
        <h3 style={{ display: "flex", alignItems: "center" }}>
          League Prime Characteristics
          <ContextInfo
            title="League Prime Characteristics"
            description="This chart shows the average prime age ranges (start, peak, end) for each league based on spline-predicted data. Allows direct comparison of typical career trajectories across different leagues."
          />
        </h3>
        <ResponsiveContainer width="100%" height={600}>
          <ScatterChart margin={{ left: 120, right: 30, top: 20, bottom: 80 }}>
            <CartesianGrid strokeDasharray="3 3" vertical={false} />
            <XAxis
              type="number"
              domain={[19, 31]}
              tickCount={10}
              allowDataOverflow={true}
              label={{
                value: "Age",
                position: "bottom",
                offset: 20,
                style: { textAnchor: "middle", fontSize: 16 },
              }}
            />
            <YAxis
              type="category"
              dataKey="league"
              tick={(props) => {
                const { x, y, payload } = props;
                return (
                  <g transform={`translate(${x},${y})`}>
                    <foreignObject width="100" height="60" x="-110" y="-20">
                      <LeagueDisplay
                        league={payload.value}
                        useLogos={useLogos}
                        showBackground={true}
                        size="large"
                      />
                    </foreignObject>
                  </g>
                );
              }}
              width={120}
              interval={0}
            />

            {/* Career Duration Lines (Light Gray) */}
            {leagueChars.map((league) => (
              <Line
                key={`career-${league.league}`}
                data={[
                  { x: league.careerStartAge, y: league.league },
                  { x: league.careerEndAge, y: league.league },
                ]}
                stroke="#cccccc"
                strokeWidth={3}
                dot={false}
              />
            ))}

            {/* Prime Duration Lines (Main Color) */}
            {leagueChars.map((league) => (
              <Line
                key={`prime-${league.league}`}
                data={[
                  { x: league.avgStartAge, y: league.league },
                  { x: league.avgEndAge, y: league.league },
                ]}
                stroke={leagueColorMapping[league.league] || "#8884d8"}
                strokeWidth={5}
                dot={false}
              />
            ))}

            {/* Career Start Markers (Gray Circles) */}
            <Scatter
              data={leagueChars}
              dataKey="careerStartAge"
              name="Career Start"
              fill="#999999"
              shape={<circle r={7} />}
            />

            {/* Prime Start Markers (Purple Circles) */}
            <Scatter
              data={leagueChars}
              dataKey="avgStartAge"
              name="Prime Start"
              fill="#8884d8"
              shape={<circle r={8} />}
            />

            {/* Peak Age Markers (Stars) */}
            <Scatter
              data={leagueChars}
              dataKey="avgPeakAge"
              name="Peak Age"
              fill="#82ca9d"
              shape={(props) => (
                <path
                  d="M12 0l3.09 6.26L22 7.27l-5 4.87 1.18 6.88L12 16l-6.18 3.02L7 12.14 2 7.27l6.91-1.01L12 0z"
                  transform={`translate(${props.cx - 12},${
                    props.cy - 12
                  }) scale(1.2)`}
                  fill="#82ca9d"
                />
              )}
            />

            {/* Prime End Markers (Orange Circles) */}
            <Scatter
              data={leagueChars}
              dataKey="avgEndAge"
              name="Prime End"
              fill="#ffc658"
              shape={<circle r={8} />}
            />

            {/* Career End Markers (Gray Circles) */}
            <Scatter
              data={leagueChars}
              dataKey="careerEndAge"
              name="Career End"
              fill="#999999"
              shape={<circle r={7} />}
            />

            {/* Add text labels directly on the chart */}
            {leagueChars.map((league, index) => (
              <React.Fragment key={`labels-${league.league}`}>
                <Text
                  x={league.avgStartAge}
                  y={index}
                  textAnchor="middle"
                  verticalAnchor="middle"
                  fill="#333"
                  style={{ fontSize: 11, fontWeight: "bold" }}
                  dy={-25}
                >
                  {league.avgStartAge}
                </Text>
                <Text
                  x={league.avgPeakAge}
                  y={index}
                  textAnchor="middle"
                  verticalAnchor="middle"
                  fill="#333"
                  style={{ fontSize: 11, fontWeight: "bold" }}
                  dy={25}
                >
                  {league.avgPeakAge}
                </Text>
                <Text
                  x={league.avgEndAge}
                  y={index}
                  textAnchor="middle"
                  verticalAnchor="middle"
                  fill="#333"
                  style={{ fontSize: 11, fontWeight: "bold" }}
                  dy={-25}
                >
                  {league.avgEndAge}
                </Text>
              </React.Fragment>
            ))}

            <Tooltip
              formatter={(value) => [`${value} years`]}
              contentStyle={{
                backgroundColor: "#fff",
                border: "1px solid #ddd",
                borderRadius: "4px",
              }}
              wrapperStyle={{ zIndex: 100 }}
            />
            <Legend
              verticalAlign="bottom"
              align="center"
              layout="horizontal"
              wrapperStyle={{ paddingTop: 30 }}
            />
          </ScatterChart>
        </ResponsiveContainer>

        {/* Add a detailed table below the chart for more data */}
        <div className="league-details-table">
          <h4>Detailed League Prime Data</h4>
          <table>
            <thead>
              <tr>
                <th>League</th>
                <th>Career Start</th>
                <th>Prime Start</th>
                <th>Peak Age</th>
                <th>Prime End</th>
                <th>Career End</th>
                <th>Prime Duration</th>
              </tr>
            </thead>
            <tbody>
              {leagueChars.map((league) => {
                return (
                  <tr key={`table-${league.league}`}>
                    <td>
                      <LeagueDisplay
                        league={league.league}
                        useLogos={useLogos}
                        size={useLogos ? "large" : "large"}
                        showBackground={true}
                        bold={true}
                      />
                    </td>
                    <td>{league.careerStartAge}</td>
                    <td>{league.avgStartAge}</td>
                    <td>{league.avgPeakAge}</td>
                    <td>{league.avgEndAge}</td>
                    <td>{league.careerEndAge}</td>
                    <td>
                      {(league.avgEndAge - league.avgStartAge + 1).toFixed(1)}{" "}
                      years
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    );
  };

  // --- Main Render ---
  return (
    <div className="prime-distribution">
      <div className="section-header">
        <h2 style={{ display: "flex", alignItems: "center" }}>
          Prime Distribution Analysis
          <ContextInfo
            title="Prime Distribution Analysis"
            description="This analysis examines how athlete prime periods are distributed across different ages, using both aggregated player data and spline-based averages. It shows when athletes are most likely to be 'In Prime' or 'At Peak Age' (based on individual player classifications), and compares average prime trajectories across leagues (based on spline model averages)."
          />
        </h2>
      </div>

      <EnhancedFilters
        allData={data?.pqiData || []} // Base filters on original data
        sports={allSports}
        leagues={leagues}
        positions={allPositions}
        selectedSports={selectedSports}
        selectedLeagues={selectedLeagues}
        selectedPositions={selectedPositions}
        onSportsChange={setSelectedSports}
        onLeaguesChange={setSelectedLeagues}
        onPositionsChange={setSelectedPositions}
        showLimit={false}
        useLogos={useLogos}
      />

      {/* Loading Indicator */}
      {isLoading && (
        <div className="loading-indicator">Processing distribution data...</div>
      )}

      {/* Render content only when not loading */}
      {!isLoading && (
        <>
          {/* Pass calculated averages to summary */}
          <PrimeDistributionSummary
            primeDistribution={primeDistribution}
            averages={averages}
          />

          {/* Chart 1: Percentage of Athletes in Prime by Age with 10+ observations filter */}
          <div className="full-width-chart">
            <h3 style={{ display: "flex", alignItems: "center" }}>
              Percentage of Athletes by Career Stage
              <ContextInfo
                title="Athletes by Career Stage"
                description="This chart shows the percentage of athletes classified as 'In Prime' (green bars) and 'At Peak Age' (orange line) at each age, based on the currently selected prime definition applied to individual player data. The gray area indicates the total number of players available at each age in the filtered dataset. Only ages with 10 or more observations are displayed for statistical reliability."
              />
            </h3>
            {primeDistribution && primeDistribution.length > 0 ? (
              (() => {
                // Filter data to only include ages with at least 10 observations
                const filteredPrimeDistribution = primeDistribution.filter(
                  (d) => d.totalPlayers >= 10
                );

                // Check if we have any data points left after filtering
                if (filteredPrimeDistribution.length === 0) {
                  return (
                    <div className="no-data">
                      No ages with sufficient data (10+ observations) available
                      for visualization.
                    </div>
                  );
                }

                return (
                  <ResponsiveContainer width="100%" height={400}>
                    <ComposedChart
                      data={filteredPrimeDistribution}
                      margin={{ top: 5, right: 30, left: 20, bottom: 30 }}
                    >
                      <CartesianGrid strokeDasharray="3 3" />
                      <XAxis
                        dataKey="age"
                        type="number"
                        domain={[18, 40]} // Restrict view to reasonable age range
                        allowDecimals={false}
                        label={{
                          value: "Age",
                          position: "insideBottom",
                          offset: -5,
                          style: { textAnchor: "middle" },
                        }}
                      />
                      <YAxis
                        yAxisId="left"
                        domain={[0, 100]}
                        label={{
                          value: "Percentage (%)",
                          angle: -90,
                          position: "insideLeft",
                          style: { textAnchor: "middle" },
                          dx: -10,
                        }}
                        tickFormatter={(value) => `${value}%`}
                      />
                      <YAxis
                        yAxisId="right"
                        orientation="right"
                        domain={[0, "auto"]}
                        tickFormatter={(value) => value.toLocaleString()}
                        label={{
                          value: "Total Players",
                          angle: 90,
                          position: "insideRight",
                          style: { textAnchor: "middle" },
                          dx: 10,
                        }}
                        allowDecimals={false}
                      />
                      <Tooltip
                        formatter={(value, name) => [
                          `${
                            typeof value === "number" ? value.toFixed(1) : "N/A"
                          }`,
                          name,
                        ]}
                      />
                      <Legend
                        wrapperStyle={{ paddingTop: 10, paddingBottom: 10 }}
                      />
                      <Area
                        yAxisId="right"
                        type="monotone"
                        dataKey="totalPlayers"
                        fill="#f0f0f0"
                        stroke="#d0d0d0"
                        name="Total Players"
                      />
                      <Bar
                        yAxisId="left"
                        dataKey="primePercentage"
                        fill="#82ca9d"
                        name="In Prime %"
                        barSize={12}
                      />
                      <Line
                        yAxisId="left"
                        type="monotone"
                        dataKey="peakPercentage"
                        stroke="#ff7300"
                        name="At Peak Age %"
                        dot={{ r: 4 }}
                        strokeWidth={2}
                        connectNulls={false}
                      />
                    </ComposedChart>
                  </ResponsiveContainer>
                );
              })()
            ) : (
              <div className="no-data">
                No data to display for prime percentages. Ensure data is
                processed correctly.
              </div>
            )}
          </div>

          {/* Chart 2: Prime Phase Distribution (Start/Peak/End Counts) */}
          {renderPrimePhaseDistribution()}

          {/* Chart 3: League Prime Characteristics (Spline Averages) */}
          {renderLeaguePrimeCharacteristics()}
        </>
      )}
    </div>
  );
};

export default PrimeDistribution;
