import React from "react";
import { useNavigate } from "react-router-dom"; // Import useNavigate
import "../styles/Navbar.css";

const Navbar = ({ activeTab, setActiveTab }) => {
  const navigate = useNavigate(); // Get navigate function

  const tabs = [
    { id: "methodology", label: "Analytics Info" },
    { id: "primeAnalysis", label: "Prime Analysis" },
    { id: "indexAnalysis", label: "Index Analysis" },
    { id: "primeIdentifier", label: "Prime Identifier" },
  ];

  const handleBrandClick = () => {
    navigate("/"); // Navigate to the root (landing page)
  };

  return (
    <div className="navbar">
      {/* Make the brand clickable */}
      <div className="navbar-brand" onClick={handleBrandClick} title="Go to Landing Page">
        peakPerformR Dashboard
      </div>
      <div className="navbar-tabs">
        {tabs.map((tab) => (
          <div
            key={tab.id}
            className={`navbar-tab ${activeTab === tab.id ? "active" : ""}`}
            onClick={() => setActiveTab(tab.id)}
          >
            {tab.label}
          </div>
        ))}
      </div>
    </div>
  );
};

export default Navbar;