// src/components/SportDisplay.js
import React from "react";
import { sportColorMapping, sportNameMapping } from "../utils/constants";
import "../styles/SportDisplay.css";

const SportDisplay = ({
  sport,
  useLogos = false,
  size = "medium",
  // Prop 'showBackground' is now repurposed to mean 'show specific text color'
  showBackground: showTextColor = false, // Rename prop internally for clarity, default false
  bold = false,
  className = "",
}) => {
  if (!sport) return <span className="sport-display-empty">--</span>;

  const displayName = sportNameMapping[sport] || sport;
  // Look up color - needed only when showTextColor is true
  const sportColor = sportColorMapping[sport] || "#6D1945"; // Fallback color

  // Base URL for logo images
  const logoPath = `${
    process.env.PUBLIC_URL
  }/images/logos/sports/${sport.toLowerCase()}.png`;

  // --- ADJUSTED LOGIC ---
  // Determine text color: specific sport color if showTextColor is true, otherwise inherit default color
  const textStyle = {
    color: showTextColor ? sportColor : "inherit", // Apply color based on the prop
    fontWeight: bold ? "700" : "500",
  };

  // Container background is ALWAYS transparent in this scenario
  const containerStyle = {
    backgroundColor: "transparent",
  };
  // --- END ADJUSTMENT ---

  // If using logos (logos mode inherently doesn't use the text color logic)
  if (useLogos) {
    return (
      <div
        // Remove background-related style/class application here for logo mode
        className={`sport-display logo-mode ${size} ${className}`}
        // style={containerStyle} // Not needed unless logo mode needs specific bg
        title={displayName}
      >
        <img
          src={logoPath}
          alt={displayName}
          className="sport-logo"
          onError={(e) => {
            /* ... error handling ... */
          }}
        />
      </div>
    );
  }

  // Text mode
  return (
    <div
      // Remove 'with-bg' class logic as background is always transparent
      className={`sport-display text-mode ${size} ${className}`}
      style={containerStyle} // Ensure background is transparent
    >
      <span className="sport-name" style={textStyle}>
        {" "}
        {/* Apply calculated text color */}
        {displayName}
      </span>
    </div>
  );
};

export default SportDisplay;
