import React, { useState, useEffect } from "react";
import "../styles/DefinitionSelector.css";

const DefinitionSelector = ({
  availableDefinitions = [],
  activeDefinitionKey,
  onDefinitionChange,
  getColorForDefinition,
  isLoading, // Receive loading state from parent
  loadingError, // Receive error state from parent
  estimatedTime, // Receive estimated time from parent
  timerActive, // Receive timer active status from parent
  loadingDefinitionKey = "", // Show which definition is loading
}) => {
  const [isExpanded, setIsExpanded] = useState(false);

  // Close dropdown if loading starts/stops or error occurs while expanded
  useEffect(() => {
    if ((isLoading || loadingError) && isExpanded) {
      // Optional: Keep it open during loading? For now, let's close it.
      setIsExpanded(false);
    }
  }, [isLoading, loadingError, isExpanded]);

  // Do not render if there are no alternate definitions (or only original)
  if (!availableDefinitions || availableDefinitions.length <= 1) {
    return null;
  }

  const handleDefinitionSelect = (definitionKey) => {
    if (definitionKey !== activeDefinitionKey && !isLoading) {
      // Prevent change while loading
      onDefinitionChange(definitionKey);
    }
    setIsExpanded(false); // Close dropdown after selection or attempt
  };

  const activeDefinition = availableDefinitions.find(
    (def) => def.id === activeDefinitionKey
  );

  const activeLabel = activeDefinition?.label ?? "Original Data";
  const isOriginal = activeDefinitionKey === "original_data_definition";
  const defaultColor = isOriginal ? "#006064" : "#ff8f00";

  const definitionColor = getColorForDefinition
    ? getColorForDefinition(activeDefinitionKey)
    : activeDefinition?.color || defaultColor;

  return (
    <div
      className={`definition-selector-wrapper ${isLoading ? "loading" : ""} ${
        loadingError ? "erroring" : ""
      }`}
    >
      <div
        className={`definition-selector-container ${
          isExpanded ? "expanded" : ""
        }`}
        style={{ borderLeftColor: definitionColor }}
        title={
          isOriginal
            ? "Using Original Prime Definition"
            : `Using Custom Definition: ${activeLabel}`
        }
      >
        <div
          className={`definition-selector-toggle ${
            isOriginal ? "original" : "custom"
          }`}
          onClick={() => !isLoading && setIsExpanded(!isExpanded)} // Prevent toggle while loading
          role="button"
          tabIndex={isLoading ? -1 : 0} // Disable focus while loading
          onKeyPress={(e) =>
            !isLoading && e.key === "Enter" && setIsExpanded(!isExpanded)
          } // Keyboard toggle
          aria-disabled={isLoading}
        >
          <div className="active-definition-info">
            <span className="definition-label">{activeLabel}</span>
            {/* Display Loading/Error Status */}
            {isLoading &&
              timerActive && ( // Show timer only when loading AND timer is active
                <span className="timer-display">
                  {loadingDefinitionKey && (
                    <span className="loading-definition">
                      {" "}
                      Calculating: {loadingDefinitionKey}
                    </span>
                  )}{" "}
                  (Est. Time: {estimatedTime}s)
                </span>
              )}
            {isLoading &&
              !timerActive && ( // Show generic loading if timer isn't active (e.g., very short load)
                <span className="loading-indicator-inline"> (Loading...)</span>
              )}
            {loadingError && (
              <span className="error-indicator-inline"> (Error!)</span>
            )}
          </div>
          {!isLoading && (
            <span className="toggle-icon">{isExpanded ? "▲" : "▼"}</span>
          )}
          {isLoading && <span className="loading-spinner-inline"></span>}{" "}
          {/* Add a visual spinner */}
        </div>

        {isExpanded &&
          !isLoading && ( // Only show dropdown if not loading
            <div className="definition-selector-dropdown">
              <div className="dropdown-header">
                <h4>Switch Prime Definition</h4>
                <button
                  className="close-dropdown"
                  onClick={(e) => {
                    e.stopPropagation();
                    setIsExpanded(false);
                  }}
                  aria-label="Close definition selector"
                >
                  ×
                </button>
              </div>
              <div className="definitions-list">
                {availableDefinitions
                  .sort((a, b) =>
                    a.id === "original_data_definition"
                      ? -1
                      : b.id === "original_data_definition"
                      ? 1
                      : 0
                  )
                  .map((definition) => {
                    const optionIsOriginal =
                      definition.id === "original_data_definition";
                    const optionDefaultColor = optionIsOriginal
                      ? "#006064"
                      : "#ff8f00";
                    const optionColor = getColorForDefinition
                      ? getColorForDefinition(definition.id)
                      : definition.color || optionDefaultColor;

                    return (
                      <div
                        key={definition.id}
                        className={`definition-option ${
                          definition.id === activeDefinitionKey ? "active" : ""
                        }`}
                        onClick={() => handleDefinitionSelect(definition.id)}
                        style={{ borderLeftColor: optionColor }}
                        role="button"
                        tabIndex={0}
                        onKeyPress={(e) =>
                          e.key === "Enter" &&
                          handleDefinitionSelect(definition.id)
                        }
                        title={`Switch to: ${definition.label}${
                          definition.parameters
                            ? ` (T:${definition.parameters.thresholdPct}%)`
                            : ""
                        }`}
                      >
                        <span className="definition-name">
                          {definition.label}
                        </span>
                        {definition.id === activeDefinitionKey && (
                          <span className="active-badge">Active</span>
                        )}

                        {/* Parameter Display Logic - Only Threshold */}
                        {definition.parameters &&
                          typeof definition.parameters.thresholdPct ===
                            "number" && (
                            <div className="definition-details">
                              <span>
                                T: {definition.parameters.thresholdPct}%
                              </span>
                            </div>
                          )}
                      </div>
                    );
                  })}
              </div>
              <div className="dropdown-footer">
                <p>Select a definition to update the dashboard view.</p>
              </div>
            </div>
          )}
      </div>
      {/* Display error message prominently if it exists */}
      {loadingError && (
        <div className="definition-error-message">Error: {loadingError}</div>
      )}
    </div>
  );
};

export default DefinitionSelector;
