// src/components/LeagueDisplay.js
import React from "react";
import {
  leagueColorMapping,
  leagueNameMapping,
  sportColorMapping,
} from "../utils/constants";
import "../styles/LeagueDisplay.css";

const LeagueDisplay = ({
  league,
  sport,
  showSport = false,
  useLogos = false,
  size = "medium",
  // Prop 'showBackground' is now repurposed to mean 'show specific text color'
  showBackground: showTextColor = false, // Rename prop internally for clarity, default false
  bold = false,
  className = "",
}) => {
  if (!league) return <span className="league-display-empty">--</span>;

  let displayName = leagueNameMapping[league] || league;
  if (league === "CHESS_M") displayName = "Chess - Male";
  else if (league === "CHESS_F") displayName = "Chess - Female";

  const combinedDisplay =
    showSport && sport ? `${sport} - ${displayName}` : displayName;

  // Get colors - needed only when showTextColor is true
  const leagueColor = leagueColorMapping[league] || "#6D1945";
  // Determine the specific color to use if text coloring is enabled
  // (Handles the 'showSport' case if used elsewhere, otherwise just uses leagueColor)
  const colorToUse =
    showSport && sport && sportColorMapping[sport]
      ? sportColorMapping[sport]
      : leagueColor;

  const logoPath = `${
    process.env.PUBLIC_URL
  }/images/logos/${league.toLowerCase()}.png`;

  // --- ADJUSTED LOGIC ---
  // Determine text color: specific color if showTextColor is true, otherwise inherit default color
  const textStyle = {
    color: showTextColor ? colorToUse : "inherit", // Apply color based on the prop
    fontWeight: bold ? "700" : "500",
  };

  // Container background is ALWAYS transparent in this scenario
  const containerStyle = {
    backgroundColor: "transparent",
  };
  // --- END ADJUSTMENT ---

  // If using logos (logos mode inherently doesn't use the text color logic)
  if (useLogos) {
    return (
      <div
        // Remove background-related style/class application here for logo mode
        className={`league-display logo-mode ${size} ${className}`}
        // style={containerStyle} // Not needed unless logo mode needs specific bg
        title={combinedDisplay}
      >
        <img
          src={logoPath}
          alt={displayName}
          className="league-logo"
          onError={(e) => {
            /* ... error handling ... */
          }}
        />
      </div>
    );
  }

  // Text mode
  return (
    <div
      // Remove 'with-bg' class logic as background is always transparent
      className={`league-display text-mode ${size} ${className}`}
      style={containerStyle} // Ensure background is transparent
    >
      <span className="league-name" style={textStyle}>
        {" "}
        {/* Apply calculated text color */}
        {combinedDisplay}
      </span>
    </div>
  );
};

export default LeagueDisplay;
