// src/components/LongestPrimes.js
import React, { useState, useEffect } from "react";
import {
  ResponsiveContainer,
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ScatterChart,
  Scatter,
  ZAxis,
  Cell,
} from "recharts";
import { getPlayersByPrimeDuration } from "../utils/dataProcessing";
import { leagueColorMapping, leagueToSportMapping } from "../utils/constants";
import EnhancedFilters from "./EnhancedFilters";
import ContextInfo from "./ContextInfo";
import LeagueDisplay from "./LeagueDisplay";
import SportDisplay from "./SportDisplay";
import PositionDisplay from "./PositionDisplay";
import "../styles/LongestPrimes.css";
import "../styles/EnhancedFilters.css";

// --- PrimeStatsSummary Component remains unchanged ---
const PrimeStatsSummary = ({ players }) => {
  if (!players || players.length === 0) return null;

  const avgPrimeDuration =
    players.reduce((sum, p) => sum + (Number(p.prime_duration) || 0), 0) /
    players.length;
  const maxPrimeDuration = Math.max(
    ...players.map((p) => Number(p.prime_duration) || 0)
  );
  const minPrimeDuration = Math.min(
    ...players.map((p) => Number(p.prime_duration) || 0)
  );

  const avgStartAge =
    players.reduce((sum, p) => sum + (Number(p.start_age) || 0), 0) /
    players.length;
  const avgEndAge =
    players.reduce((sum, p) => sum + (Number(p.end_age) || 0), 0) /
    players.length;
  const avgPeakAge =
    players.reduce((sum, p) => sum + (Number(p.max_value_age) || 0), 0) /
    players.length;

  return (
    <div className="prime-stats-summary">
      <h3 style={{ display: "flex", alignItems: "center" }}>
        Prime Statistics Summary
        <ContextInfo
          title="Prime Statistics Summary"
          description="This table shows aggregate statistics about athlete prime periods. It includes average, minimum, and maximum prime durations, as well as the typical ages at which athletes begin, peak, and conclude their prime years."
        />
      </h3>
      <div className="stats-grid">
        <div className="stat-column">
          <h4>Prime Duration</h4>
          <div className="stat-row">
            <div className="stat-label">Average:</div>
            <div className="stat-value">
              {avgPrimeDuration.toFixed(1)} years
            </div>
          </div>
          <div className="stat-row">
            <div className="stat-label">Maximum:</div>
            <div className="stat-value">
              {maxPrimeDuration.toFixed(1)} years
            </div>
          </div>
          <div className="stat-row">
            <div className="stat-label">Minimum:</div>
            <div className="stat-value">
              {minPrimeDuration.toFixed(1)} years
            </div>
          </div>
        </div>

        <div className="stat-column">
          <h4>Key Ages</h4>
          <div className="stat-row">
            <div className="stat-label">Avg. Start Age:</div>
            <div className="stat-value">{avgStartAge.toFixed(1)} years</div>
          </div>
          <div className="stat-row">
            <div className="stat-label">Avg. Peak Age:</div>
            <div className="stat-value">{avgPeakAge.toFixed(1)} years</div>
          </div>
          <div className="stat-row">
            <div className="stat-label">Avg. End Age:</div>
            <div className="stat-value">{avgEndAge.toFixed(1)} years</div>
          </div>
        </div>

        <div className="stat-column">
          <h4>Data Sample</h4>
          <div className="stat-row">
            <div className="stat-label">Players Analyzed:</div>
            <div className="stat-value">{players.length}</div>
          </div>
          <div className="stat-row">
            <div className="stat-label">Sports Included:</div>
            <div className="stat-value">
              {new Set(players.map((p) => p.sport)).size}
            </div>
          </div>
          <div className="stat-row">
            <div className="stat-label">Leagues Included:</div>
            <div className="stat-value">
              {new Set(players.map((p) => p.league)).size}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

// --- LongestPrimes Component ---
const LongestPrimes = ({
  data,
  positions,
  leagues,
  selectedSports = [],
  selectedLeagues = [],
  selectedPositions = [],
  limit = 25,
  setSelectedSports = () => {},
  setSelectedLeagues = () => {},
  setSelectedPositions = () => {},
  setLimit = () => {},
  useLogos = false,
  toggleLogos = () => {},
}) => {
  const [viewMode, setViewMode] = useState("table");
  const [players, setPlayers] = useState([]);

  const allSports = React.useMemo(() => {
    if (!data || !data.pqiData) return [];
    return [...new Set(data.pqiData.map((player) => player.sport))].filter(
      Boolean
    );
  }, [data]);

  useEffect(() => {
    if (data) {
      const filters = {
        sport: selectedSports.length > 0 ? selectedSports : undefined,
        league: selectedLeagues.length > 0 ? selectedLeagues : undefined,
        position: selectedPositions.length > 0 ? selectedPositions : undefined,
      };
      const limitValue = limit === 0 ? 7000 : limit;
      const longestPrimePlayers = getPlayersByPrimeDuration(
        data.primesSplineData,
        data.pqiData,
        limitValue,
        filters
      );
      setPlayers(longestPrimePlayers);
    }
  }, [data, selectedSports, selectedLeagues, selectedPositions, limit]);

  // --- renderTableView ---
  const renderTableView = () => {
    return (
      <div className="table-view">
        <PrimeStatsSummary players={players} />
        <h3 style={{ display: "flex", alignItems: "center" }}>
          Players with Longest Primes
          <ContextInfo
            title="Longest Prime List"
            description="This table ranks athletes by their prime duration in years. A player's prime is defined as the continuous period where performance remains above a 70% threshold percentage of peak value. The table shows when their prime started, ended, and reached its peak."
          />
        </h3>
        <table className="player-table">
          <thead>
            <tr>
              <th>Rank</th>
              <th>Player</th>
              <th>Sport</th>
              <th>League</th>
              <th>Position</th>
              <th>Prime Duration</th>
              <th>Start Age</th>
              <th>End Age</th>
              <th>Peak Age</th>
              <th>PQI Score</th>
            </tr>
          </thead>
          <tbody>
            {players.map((player, index) => (
              <tr key={player.id}>
                <td>{index + 1}</td>
                <td>{player.player_name}</td>
                <td>
                  {useLogos ? (
                    <SportDisplay
                      sport={player.sport}
                      useLogos={true}
                      size="medium"
                    />
                  ) : (
                    <SportDisplay sport={player.sport} size="large" />
                  )}
                </td>
                <td>
                  <LeagueDisplay
                    league={player.league}
                    useLogos={useLogos}
                    size="medium"
                  />
                </td>
                <td>
                  <PositionDisplay
                    position={player.position}
                    league={player.league}
                    sport={player.sport}
                    showFull={true}
                  />
                </td>
                <td>{player.prime_duration}</td>
                <td>{player.start_age}</td>
                <td>{player.end_age}</td>
                <td>{player.max_value_age}</td>
                <td>{player.pqi_selected?.toFixed(2)}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  };

  // --- renderChartView --- FIXED to use LeagueDisplay consistently
  const renderChartView = () => {
    // Prepare data
    const barData = players.slice(0, 15).map((player) => ({
      name: player.player_name,
      duration: player.prime_duration,
      league: player.league,
      sport: player.sport,
    }));

    const scatterData = players.map((player) => ({
      x: player.start_age,
      y: player.prime_duration,
      name: player.player_name,
      league: player.league,
      sport: player.sport,
      z: player.pqi_selected || 1,
    }));

    const leagueMap = {};
    players.forEach((player) => {
      if (!leagueMap[player.league]) {
        leagueMap[player.league] = {
          league: player.league,
          players: [],
          avgDuration: 0,
          avgStartAge: 0,
          avgEndAge: 0,
          sport: player.sport,
        };
      }
      leagueMap[player.league].players.push(player);
    });
    const leagueData = Object.keys(leagueMap)
      .map((key) => {
        const leaguePlayers = leagueMap[key].players;
        leagueMap[key].avgDuration =
          leaguePlayers.reduce((sum, p) => sum + p.prime_duration, 0) /
          leaguePlayers.length;
        leagueMap[key].avgStartAge =
          leaguePlayers.reduce((sum, p) => sum + p.start_age, 0) /
          leaguePlayers.length;
        leagueMap[key].avgEndAge =
          leaguePlayers.reduce((sum, p) => sum + p.end_age, 0) /
          leaguePlayers.length;
        return leagueMap[key];
      })
      .sort((a, b) => b.avgDuration - a.avgDuration); // Sort here

    // Custom tooltip component for bar chart
    const BarTooltip = ({ active, payload }) => {
      if (!active || !payload || !payload.length) return null;

      const data = payload[0].payload;
      return (
        <div
          className="custom-tooltip"
          style={{
            backgroundColor: "white",
            padding: "10px",
            border: "1px solid #ccc",
            borderRadius: "4px",
          }}
        >
          <p className="tooltip-name">{data.name}</p>
          <div className="tooltip-sport">
            {useLogos ? (
              <span
                style={{
                  display: "inline-flex",
                  alignItems: "center",
                  gap: "5px",
                }}
              >
                <SportDisplay
                  sport={data.sport}
                  useLogos={true}
                  size="medium"
                />
                <LeagueDisplay
                  league={data.league}
                  useLogos={true}
                  size="medium"
                />
              </span>
            ) : (
              <span>
                {data.sport} / <LeagueDisplay league={data.league} />
              </span>
            )}
          </div>
          <p className="tooltip-duration">
            <strong>Duration:</strong> {data.duration} years
          </p>
        </div>
      );
    };

    // Custom tooltip component for scatter chart
    const ScatterTooltip = ({ active, payload }) => {
      if (!active || !payload || !payload.length) return null;

      const data = payload[0].payload;
      return (
        <div
          className="custom-tooltip"
          style={{
            backgroundColor: "white",
            padding: "10px",
            border: "1px solid #ccc",
            borderRadius: "4px",
          }}
        >
          <p className="tooltip-name">{data.name}</p>
          <div className="tooltip-sport">
            {useLogos ? (
              <span
                style={{
                  display: "inline-flex",
                  alignItems: "center",
                  gap: "5px",
                }}
              >
                <SportDisplay
                  sport={data.sport}
                  useLogos={true}
                  size="medium"
                />
                <LeagueDisplay
                  league={data.league}
                  useLogos={true}
                  size="medium"
                />
              </span>
            ) : (
              <span>
                {data.sport} / <LeagueDisplay league={data.league} />
              </span>
            )}
          </div>
          <p className="tooltip-stat">
            Start Age: <strong>{data.x}</strong>
          </p>
          <p className="tooltip-stat">
            Duration: <strong>{data.y} years</strong>
          </p>
          <p className="tooltip-stat">
            PQI: <strong>{data.z.toFixed(2)}</strong>
          </p>
        </div>
      );
    };

    // Custom tooltip component for bar chart (vertical)
    const BarVerticalTooltip = ({ active, payload }) => {
      if (!active || !payload || !payload.length) return null;

      const league = payload[0]?.payload?.league;
      if (!league) return null;

      const inferredSport = leagueToSportMapping[league] || "Unknown";

      return (
        <div
          className="custom-tooltip"
          style={{
            backgroundColor: "white",
            padding: "10px",
            border: "1px solid #ccc",
            borderRadius: "4px",
          }}
        >
          <div style={{ marginBottom: "5px" }}>
            {useLogos ? (
              <span
                style={{ display: "flex", alignItems: "center", gap: "5px" }}
              >
                <SportDisplay
                  sport={inferredSport}
                  useLogos={true}
                  size="medium"
                />
                <LeagueDisplay league={league} useLogos={true} size="medium" />
              </span>
            ) : (
              <span>
                <SportDisplay sport={inferredSport} /> /{" "}
                <LeagueDisplay league={league} />
              </span>
            )}
          </div>
          <p>
            Avg. Duration: <strong>{payload[0].value.toFixed(2)}</strong>
          </p>
        </div>
      );
    };

    return (
      <div className="chart-view">
        {/* --- Top 15 Players Bar Chart --- */}
        <div className="chart-container">
          <h3 style={{ display: "flex", alignItems: "center" }}>
            Top 15 Players by Prime Duration
            <ContextInfo
              title="Top Players Chart"
              description="This chart shows the top 15 athletes with the longest prime duration periods. Prime duration represents the continuous time span where an athlete maintains performance above the threshold percentage of their peak value."
            />
          </h3>
          <ResponsiveContainer width="100%" height={400}>
            <BarChart
              data={barData}
              margin={{ top: 20, right: 30, left: 20, bottom: 120 }}
            >
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis
                dataKey="name"
                angle={-45}
                textAnchor="end"
                interval={0}
                tick={(props) => {
                  const { x, y, payload } = props;
                  if (!payload || typeof payload.value === "undefined")
                    return null;
                  const player = barData.find((p) => p.name === payload.value);
                  return (
                    <g transform={`translate(${x},${y})`}>
                      <text
                        x={0}
                        y={0}
                        dy={16}
                        textAnchor="end"
                        fill="#666"
                        transform="rotate(-45)"
                        fontSize="12"
                      >
                        {payload.value}
                      </text>
                      {useLogos && player && (
                        <g transform="translate(-18, -15)">
                          <foreignObject width="20" height="20" x={0} y={0}>
                            <LeagueDisplay
                              league={player.league}
                              useLogos={true}
                              size="small"
                            />
                          </foreignObject>
                        </g>
                      )}
                    </g>
                  );
                }}
              />
              <YAxis
                label={{
                  value: "Prime Duration (Years)",
                  angle: -90,
                  position: "insideLeft",
                  dy: 40,
                  style: { textAnchor: "middle" },
                }}
                tick={{ fontSize: 12 }}
              />
              <Tooltip content={<BarTooltip />} />
              <Bar dataKey="duration" name="Prime Duration">
                {barData.map((entry, index) => (
                  <Cell
                    key={`cell-${index}`}
                    fill={leagueColorMapping[entry.league] || "#8884d8"}
                  />
                ))}
              </Bar>
            </BarChart>
          </ResponsiveContainer>
        </div>

        <div className="charts-row">
          {/* --- Prime Start Age vs. Duration Scatter Plot --- */}
          <div className="chart-container half-width">
            <h3 style={{ display: "flex", alignItems: "center" }}>
              Prime Start Age vs. Duration
              <ContextInfo
                title="Start Age vs Duration"
                description="This scatter plot analyzes the relationship between when an athlete's prime begins (x-axis) and how long it lasts (y-axis). Each point represents a player, and colors indicate different leagues."
              />
            </h3>
            <ResponsiveContainer width="100%" height={400}>
              <ScatterChart
                margin={{ top: 20, right: 20, bottom: 20, left: 20 }}
              >
                <CartesianGrid />
                <XAxis
                  type="number"
                  dataKey="x"
                  name="Start Age"
                  label={{
                    value: "Prime Start Age",
                    position: "insideBottom",
                    offset: -5,
                    style: { textAnchor: "middle" },
                  }}
                  tick={{ fontSize: 12 }}
                />
                <YAxis
                  type="number"
                  dataKey="y"
                  name="Duration"
                  label={{
                    value: "Prime Duration (Years)",
                    angle: -90,
                    position: "insideLeft",
                    dy: 40,
                    style: { textAnchor: "middle" },
                  }}
                  tick={{ fontSize: 12 }}
                />
                <ZAxis type="number" dataKey="z" range={[50, 400]} name="PQI" />
                <Tooltip content={<ScatterTooltip />} />
                <Legend
                  formatter={(value) => {
                    if (useLogos) {
                      return (
                        <span
                          style={{
                            display: "inline-flex",
                            alignItems: "center",
                            verticalAlign: "middle",
                            marginLeft: "5px",
                          }}
                        >
                          <LeagueDisplay
                            league={value}
                            useLogos={true}
                            size="small"
                          />
                        </span>
                      );
                    }
                    return (
                      <span
                        style={{ marginLeft: "5px", verticalAlign: "middle" }}
                      >
                        <LeagueDisplay league={value} />
                      </span>
                    );
                  }}
                />
                {Object.keys(leagueMap).map((league) => (
                  <Scatter
                    key={league}
                    name={league}
                    data={scatterData.filter((d) => d.league === league)}
                    fill={leagueColorMapping[league] || "#8884d8"}
                  />
                ))}
              </ScatterChart>
            </ResponsiveContainer>
          </div>

          {/* --- League Comparison Bar Chart --- */}
          <div className="chart-container half-width">
            <h3 style={{ display: "flex", alignItems: "center" }}>
              League Comparison
              <ContextInfo
                title="League Comparison"
                description="This chart compares the average prime duration across different leagues. It provides insight into which sports/leagues tend to have longer athlete prime periods."
              />
            </h3>
            <ResponsiveContainer width="100%" height={400}>
              <BarChart
                data={leagueData}
                layout="vertical"
                margin={{ top: 20, right: 30, left: 30, bottom: 20 }}
              >
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis
                  type="number"
                  label={{
                    value: "Average Prime Duration (Years)",
                    position: "insideBottom",
                    offset: 0,
                    style: { textAnchor: "middle" },
                  }}
                  tick={{ fontSize: 12 }}
                />
                <YAxis
                  type="category"
                  dataKey="league"
                  width={80}
                  interval={0}
                  tick={(props) => {
                    const { x, y, payload } = props;
                    // Basic validation
                    if (
                      !payload ||
                      typeof payload.value === "undefined" ||
                      payload.value === null
                    ) {
                      return null;
                    }
                    const leagueName = payload.value; // The league identifier (e.g., "NBA")

                    // Calculate a suitable y offset to center vertically
                    const yOffset = 0; // Adjust if needed based on font/logo size

                    return (
                      <g transform={`translate(${x},${y + yOffset})`}>
                        {useLogos ? (
                          // foreignObject for rendering HTML/React components (like images in LeagueDisplay)
                          <foreignObject
                            // Position slightly left of the axis line, adjust x offset as needed
                            x={-75}
                            // Center vertically around the tick position (adjust y based on height)
                            y={-12}
                            width="70" // Ensure enough width for logo + text
                            height="24" // Adjust height to fit content
                            style={{ overflow: "visible", textAlign: "right" }} // Allow overflow, align right
                          >
                            {/* The div inside is less critical now, but can help with alignment if needed */}
                            <div
                              xmlns="http://www.w3.org/1999/xhtml" // Required for foreignObject content
                              style={{
                                display: "flex", // Use flex to easily align
                                alignItems: "center",
                                justifyContent: "flex-end", // Align content to the right edge
                                height: "100%",
                                paddingRight: "5px", // Add some padding from the axis line
                              }}
                            >
                              <LeagueDisplay
                                league={leagueName}
                                useLogos={true}
                                // Removed showBackground={true} as it might interfere
                                size="medium" // Keep size consistent
                              />
                            </div>
                          </foreignObject>
                        ) : (
                          // Simple text rendering when not using logos
                          <text
                            x={-8} // Position text slightly left of the axis line
                            y={4} // Slight vertical adjustment for text baseline
                            textAnchor="end" // Align text to the end (right side)
                            fill="#666"
                            fontSize="12"
                          >
                            {/* Render the league name directly */}
                            {leagueName}
                          </text>
                        )}
                      </g>
                    );
                  }}
                />
                <Tooltip content={<BarVerticalTooltip />} />
                <Bar dataKey="avgDuration" name="Avg. Prime Duration">
                  {leagueData.map((entry, index) => (
                    <Cell
                      key={`cell-${index}`}
                      fill={leagueColorMapping[entry.league] || "#8884d8"}
                    />
                  ))}
                </Bar>
              </BarChart>
            </ResponsiveContainer>
          </div>
        </div>
      </div>
    );
  };

  // --- renderComparisonView --- FIXED to properly display leagues
  const renderComparisonView = () => {
    // Group by league
    const leagueMap = {};
    players.forEach((player) => {
      if (!leagueMap[player.league]) {
        leagueMap[player.league] = [];
      }
      leagueMap[player.league].push(player);
    });

    // Calculate stats by league
    const leagueStats = Object.keys(leagueMap)
      .map((league) => {
        const leaguePlayers = leagueMap[league];
        const avgStartAge =
          leaguePlayers.reduce((sum, p) => sum + p.start_age, 0) /
          leaguePlayers.length;
        const avgEndAge =
          leaguePlayers.reduce((sum, p) => sum + p.end_age, 0) /
          leaguePlayers.length;
        const avgPeakAge =
          leaguePlayers.reduce((sum, p) => sum + p.max_value_age, 0) /
          leaguePlayers.length;
        const avgDuration =
          leaguePlayers.reduce((sum, p) => sum + p.prime_duration, 0) /
          leaguePlayers.length;
        const mainSport =
          leagueToSportMapping[league] || leaguePlayers[0]?.sport || "Unknown";

        return {
          league,
          sport: mainSport,
          avgStartAge: avgStartAge.toFixed(1),
          avgEndAge: avgEndAge.toFixed(1),
          avgPeakAge: avgPeakAge.toFixed(1),
          avgDuration: avgDuration.toFixed(1),
          count: leaguePlayers.length,
        };
      })
      .sort((a, b) => b.avgDuration - a.avgDuration);

    const maxObservedAge = Math.max(
      1,
      ...leagueStats.map((stat) => parseFloat(stat.avgEndAge) + 5)
    );
    const minObservedAge = Math.min(
      maxObservedAge - 1,
      ...leagueStats.map((stat) => parseFloat(stat.avgStartAge) - 2)
    );

    return (
      <div className="comparison-view">
        <h3 style={{ display: "flex", alignItems: "center" }}>
          League Comparison
          <ContextInfo
            title="League Comparison"
            description="This view compares prime age metrics across different leagues, including when athletes typically start and end their prime, and the average duration of prime years."
          />
        </h3>
        <table className="comparison-table">
          <thead>
            <tr>
              <th>League</th>
              {useLogos && <th>Sport</th>}
              <th>Avg. Start Age</th>
              <th>Avg. Peak Age</th>
              <th>Avg. End Age</th>
              <th>Avg. Prime Duration</th>
              <th>Players</th>
            </tr>
          </thead>
          <tbody>
            {leagueStats.map((stat) => (
              <tr key={stat.league}>
                <td style={{ fontWeight: "bold" }}>
                  <LeagueDisplay
                    league={stat.league}
                    useLogos={useLogos}
                    size="medium"
                    showBackground={false}
                  />
                </td>
                {useLogos && (
                  <td>
                    <SportDisplay
                      sport={stat.sport}
                      useLogos={true}
                      size="medium"
                      showBackground={false}
                    />
                  </td>
                )}
                <td>{stat.avgStartAge}</td>
                <td>{stat.avgPeakAge}</td>
                <td>{stat.avgEndAge}</td>
                <td>{stat.avgDuration}</td>
                <td>{stat.count}</td>
              </tr>
            ))}
          </tbody>
        </table>

        <div className="prime-timeline-visual">
          <h3 style={{ display: "flex", alignItems: "center" }}>
            Prime Age Ranges by League
            <ContextInfo
              title="Prime Age Timeline"
              description="This timeline visualization shows the prime age span for each league. Colored blocks represent the prime years, and star markers indicate the peak performance age."
            />
          </h3>
          <div className="timeline-container">
            {leagueStats.map((stat) => {
              const start = parseFloat(stat.avgStartAge);
              const end = parseFloat(stat.avgEndAge);
              const primeDuration =
                isNaN(start) || isNaN(end) || end < start ? 0 : end - start + 1;
              const peak = parseFloat(stat.avgPeakAge);
              return (
                <div className="timeline-row" key={stat.league}>
                  <div
                    className="timeline-label"
                    style={{
                      color: leagueColorMapping[stat.league] || "#333",
                      display: "flex",
                      alignItems: "center",
                      gap: "5px",
                    }}
                  >
                    {useLogos ? (
                      <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          gap: "5px",
                        }}
                      >
                        <LeagueDisplay
                          league={stat.league}
                          useLogos={true}
                          size="medium"
                          showBackground={false}
                        />
                        <SportDisplay
                          sport={stat.sport}
                          useLogos={true}
                          size="medium"
                          showBackground={false}
                        />
                      </div>
                    ) : (
                      <LeagueDisplay league={stat.league} />
                    )}
                  </div>
                  <div className="timeline-bar">
                    {Array.from(
                      {
                        length: Math.max(
                          1,
                          Math.ceil(maxObservedAge - minObservedAge)
                        ),
                      },
                      (_, i) => i + Math.floor(minObservedAge)
                    ).map((age) => {
                      const isInPrime =
                        !isNaN(start) &&
                        !isNaN(end) &&
                        age >= start &&
                        age <= end;
                      const isPeak = !isNaN(peak) && Math.round(peak) === age;
                      return (
                        <div
                          key={age}
                          className={`timeline-age ${
                            isInPrime ? "in-prime" : ""
                          } ${isPeak ? "peak-age" : ""}`}
                          style={{
                            backgroundColor: isInPrime
                              ? leagueColorMapping[stat.league] || "#ccc"
                              : "transparent",
                            opacity: isInPrime ? 0.7 : 0.1,
                          }}
                        >
                          {isPeak && <div className="peak-marker">★</div>}
                          <div className="age-label">{age}</div>
                        </div>
                      );
                    })}
                  </div>
                  <div className="timeline-info">
                    {primeDuration > 0 && (
                      <span>Prime: {primeDuration.toFixed(1)} years</span>
                    )}
                    {!isNaN(start) && !isNaN(end) && (
                      <span>
                        ({stat.avgStartAge} - {stat.avgEndAge})
                      </span>
                    )}
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    );
  };

  // --- Main component return structure ---
  return (
    <div className="longest-primes">
      <div className="section-header">
        <h2 style={{ display: "flex", alignItems: "center" }}>
          Longest Primes
          <ContextInfo
            title="Longest Primes Analysis"
            description="This analysis identifies and compares athletes with the longest prime performance periods across different sports and leagues. It provides rankings, visualizations, and comparisons of prime duration statistics."
          />
        </h2>
        <div className="view-toggles">
          <button
            className={viewMode === "table" ? "active" : ""}
            onClick={() => setViewMode("table")}
          >
            Table View
          </button>
          <button
            className={viewMode === "chart" ? "active" : ""}
            onClick={() => setViewMode("chart")}
          >
            Chart View
          </button>
          <button
            className={viewMode === "comparison" ? "active" : ""}
            onClick={() => setViewMode("comparison")}
          >
            Comparison View
          </button>
        </div>
      </div>

      <EnhancedFilters
        allData={data?.pqiData || []}
        sports={allSports}
        leagues={leagues}
        positions={positions}
        selectedSports={selectedSports}
        selectedLeagues={selectedLeagues}
        selectedPositions={selectedPositions}
        limit={limit}
        onSportsChange={setSelectedSports}
        onLeaguesChange={setSelectedLeagues}
        onPositionsChange={setSelectedPositions}
        onLimitChange={setLimit}
        showLimit={true}
        useLogos={useLogos}
      />

      <div className="results-section">
        {viewMode === "table" && renderTableView()}
        {viewMode === "chart" && renderChartView()}
        {viewMode === "comparison" && renderComparisonView()}
      </div>
    </div>
  );
};

export default LongestPrimes;
