// src/utils/dataProcessing.js
import _ from "lodash";
import { pqiTiers, cqiTiers } from "./constants"; // Import tier lists for safety

// --- Helper Functions ---

/**
 * Safely parses a value to a float, returning null if invalid.
 */
const safeParseFloat = (value) => {
  if (typeof value === "number" && !isNaN(value)) {
    return value;
  }
  if (value === null || value === undefined || value === "") {
    return null;
  }
  const num = parseFloat(String(value).trim());
  return isNaN(num) ? null : num;
};

/**
 * Safely checks if a value is a boolean, accounting for string representations
 */
const isBooleanValue = (value) => {
  return (
    value === true ||
    value === false ||
    value === "true" ||
    value === "false" ||
    value === 1 ||
    value === 0
  );
};

/**
 * Safely converts a value to a boolean
 */
const toBooleanValue = (value) => {
  if (value === true || value === 1 || value === "true") return true;
  if (value === false || value === 0 || value === "false") return false;
  return null; // Return null if conversion is uncertain
};

/**
 * Determines the performance tier based on a score and tier type.
 */
const getTier = (score, tierType = "pqi") => {
  const numericScore = safeParseFloat(score);
  if (numericScore === null) return "N/A";

  const tiers = tierType === "pqi" ? pqiTiers : cqiTiers;

  // Ensure tiers is an array before accessing elements
  if (!Array.isArray(tiers) || tiers.length < 5) {
    console.warn(`Tier definition missing or incomplete for type: ${tierType}`);
    return "N/A";
  }

  // PQI Tiers (Assuming 0-100 score range)
  if (tierType === "pqi") {
    if (numericScore >= 95) return tiers[0]; // Hall of Fame
    if (numericScore >= 75) return tiers[1]; // Elite Player
    if (numericScore >= 45) return tiers[2]; // Great Starter
    if (numericScore >= 15) return tiers[3]; // Starter
    return tiers[4]; // Backup
  }

  // CQI Tiers (Using R code thresholds - adjust if needed)
  if (tierType === "cqi") {
    if (numericScore >= 95) return tiers[0]; // Hall of Fame
    if (numericScore >= 85) return tiers[1]; // Elite Career
    if (numericScore >= 70) return tiers[2]; // Great Career
    if (numericScore >= 40) return tiers[3]; // Solid Career
    return tiers[4]; // Limited Career
  }

  return "N/A";
};

// --- API Data Processing Implementation ---
/**
 * Processes the raw JSON response from the /recalculate API endpoint.
 */
export const processApiData = (apiResponse, definitionKey) => {
  console.log(
    `Processing API data for definition: ${definitionKey}` /* apiResponse */
  ); // Avoid logging large objects in prod

  const {
    fullData: apiFullData = [],
    splinePrimes: apiSplinePrimes = [],
    pqi: apiPqi = [],
    cqi: apiCqi = [],
    rawPrimes: apiRawPrimes = [],
    playerPredictions: apiPlayerPredictions = [],
  } = apiResponse || {}; // Use default empty arrays if missing

  // Basic check for essential data presence
  if (
    apiFullData.length === 0 ||
    apiPqi.length === 0 ||
    apiCqi.length === 0 ||
    apiSplinePrimes.length === 0
  ) {
    console.error(
      `processApiData (${definitionKey}): Essential data arrays (fullData, pqi, cqi, splinePrimes) missing or empty in API response.`
    );
    // Depending on requirements, you might throw an error or return a specific structure indicating failure
    // return null; or throw new Error('Essential data missing');
  }

  // --- Process Spline Primes ---
  const splinePrimesById = new Map();
  apiSplinePrimes.forEach((p) => {
    if (p && p.id != null) {
      const startAge = safeParseFloat(p.start_age);
      const maxValAge = safeParseFloat(p.max_value_age);
      const endAge = safeParseFloat(p.end_age);
      const duration =
        startAge !== null && endAge !== null && endAge >= startAge
          ? endAge - startAge + 1
          : safeParseFloat(p.prime_duration);
      splinePrimesById.set(p.id, {
        ...p,
        start_age: startAge,
        max_value_age: maxValAge,
        end_age: endAge,
        prime_duration: duration !== null && duration > 0 ? duration : null,
      });
    }
  });

  // --- Enhance fullData ---
  let missingPrimeInfoCount = 0;
  const enhancedFullData = apiFullData
    .map((season) => {
      if (!season || season.id == null) return null;
      const age = safeParseFloat(season.age);
      if (age === null) return null;
      const playerSplinePrime = splinePrimesById.get(season.id);
      let in_prime = isBooleanValue(season.in_prime)
        ? toBooleanValue(season.in_prime)
        : false;
      let is_peak_age = isBooleanValue(season.is_peak_age)
        ? toBooleanValue(season.is_peak_age)
        : false;
      let years_from_peak = safeParseFloat(season.years_from_peak);
      if (playerSplinePrime) {
        const { start_age, max_value_age, end_age } = playerSplinePrime;
        if (start_age !== null && end_age !== null) {
          in_prime = age >= start_age && age <= end_age;
        }
        if (max_value_age !== null) {
          is_peak_age = age === max_value_age;
          years_from_peak = age - max_value_age;
        }
      } else {
        missingPrimeInfoCount++;
      }
      return {
        ...season,
        age,
        player_value: safeParseFloat(season.player_value),
        scaled_value: safeParseFloat(season.scaled_value),
        games_played: safeParseFloat(season.games_played),
        in_prime,
        is_peak_age,
        years_from_peak,
      };
    })
    .filter(Boolean); // Remove nulls
  if (missingPrimeInfoCount > 0)
    console.warn(
      `processApiData: ${missingPrimeInfoCount} fullData seasons lacked corresponding spline prime info.`
    );

  // --- Process PQI Data ---
  const processedPqiData = apiPqi
    .map((p) => {
      if (!p || p.id == null) return null;
      const score = safeParseFloat(p.pqi_selected); // Use _selected
      const tier = getTier(score, "pqi");
      if (score === null) return null; // Skip if score is invalid
      return {
        ...p,
        pqi_selected: score,
        selected_tier: tier,
        career_seasons: safeParseFloat(p.career_seasons),
        prime_seasons: safeParseFloat(p.prime_seasons),
        prime_density: safeParseFloat(p.prime_density),
        career_games: safeParseFloat(p.career_games),
        prime_peak_value: safeParseFloat(p.prime_peak_value),
        prime_avg_value: safeParseFloat(p.prime_avg_value),
        prime_avg_tier: safeParseFloat(p.prime_avg_tier),
        is_active: toBooleanValue(p.is_active) ?? false,
      };
    })
    .filter(Boolean);

  // --- Process CQI Data ---
  const processedCqiData = apiCqi
    .map((c) => {
      if (!c || c.id == null) return null;
      const score = safeParseFloat(c.cqi_selected); // Use _selected
      const tier = getTier(score, "cqi");
      if (score === null) return null;
      return {
        ...c,
        cqi_selected: score,
        selected_tier: tier,
        career_seasons: safeParseFloat(c.career_seasons),
        career_games: safeParseFloat(c.career_games),
        career_avg_tier: safeParseFloat(c.career_avg_tier),
        career_avg_value: safeParseFloat(c.career_avg_value),
        career_peak_value: safeParseFloat(c.career_peak_value),
        is_active: toBooleanValue(c.is_active) ?? false,
      };
    })
    .filter(Boolean);

  // --- Process Raw Primes ---
  const processedPrimesRawData = apiRawPrimes
    .map((p) => {
      if (!p || p.id == null) return null;
      const startAge = safeParseFloat(p.start_age);
      const endAge = safeParseFloat(p.end_age);
      const duration =
        startAge !== null && endAge !== null && endAge >= startAge
          ? endAge - startAge + 1
          : safeParseFloat(p.prime_duration);
      return {
        ...p,
        start_age: startAge,
        max_value_age: safeParseFloat(p.max_value_age),
        end_age: endAge,
        prime_duration: duration !== null && duration > 0 ? duration : null,
      };
    })
    .filter(Boolean);

  // --- Process Spline Primes ---
  const processedPrimesSplineData = Array.from(splinePrimesById.values());

  // --- Process Predictions ---
  const processedPredictions = apiPlayerPredictions
    .map((p) => {
      if (!p || p.id == null || p.age == null) return null;
      return {
        ...p,
        age: safeParseFloat(p.age),
        predicted_value: safeParseFloat(p.predicted_value),
      };
    })
    .filter((p) => p !== null && p.age !== null);

  // --- Assemble Final Data Structure ---
  const finalData = {
    fullData: enhancedFullData,
    pqiData: processedPqiData,
    cqiData: processedCqiData,
    primesRawData: processedPrimesRawData,
    primesSplineData: processedPrimesSplineData,
    playerPredictions: processedPredictions,
    definition_key: definitionKey,
  };

  console.log(
    `Data processed for ${definitionKey}. Final Counts: PQI=${finalData.pqiData.length}, CQI=${finalData.cqiData.length}, Full=${finalData.fullData.length}, Spline=${finalData.primesSplineData.length}`
  );
  if (
    finalData.pqiData.length === 0 ||
    finalData.cqiData.length === 0 ||
    finalData.fullData.length === 0 ||
    finalData.primesSplineData.length === 0
  ) {
    console.error(
      `CRITICAL ERROR processing ${definitionKey}: Essential data arrays empty AFTER processing.`
    );
    // Potentially throw error here if needed throw new Error('Processed data arrays are empty');
  }

  return finalData;
};

// --- Core Data Analysis Functions ---

export const getPrimeDistributionByAge = (fullPlayerData) => {
  if (!fullPlayerData || fullPlayerData.length === 0) return [];
  const allAges = fullPlayerData
    .map((p) => p.age)
    .filter(
      (age) => typeof age === "number" && !isNaN(age) && age >= 15 && age <= 50
    );
  if (allAges.length === 0) return []; // No valid ages found
  const minAge = Math.min(...allAges);
  const maxAge = Math.max(...allAges);
  const ageBins = {};
  for (let age = minAge; age <= maxAge; age++) {
    ageBins[age] = { totalPlayers: 0, inPrimeCount: 0, atPeakCount: 0 };
  }
  fullPlayerData.forEach((player) => {
    const age = player.age;
    if (typeof age !== "number" || isNaN(age) || age < minAge || age > maxAge)
      return;
    ageBins[age].totalPlayers++;
    if (toBooleanValue(player.in_prime) === true) ageBins[age].inPrimeCount++;
    if (toBooleanValue(player.is_peak_age) === true) ageBins[age].atPeakCount++;
  });
  return Object.entries(ageBins)
    .map(([ageStr, counts]) => {
      const age = parseInt(ageStr);
      const { totalPlayers, inPrimeCount, atPeakCount } = counts;
      const primePercentage =
        totalPlayers > 0 ? (inPrimeCount / totalPlayers) * 100 : 0;
      const peakPercentage =
        totalPlayers > 0 ? (atPeakCount / totalPlayers) * 100 : 0;
      return {
        age,
        totalPlayers,
        primeCount: inPrimeCount,
        peakCount: atPeakCount,
        primePercentage,
        peakPercentage,
      };
    })
    .sort((a, b) => a.age - b.age);
};

export const getAgingCurvesByLeague = (fullPlayerData) => {
  if (!fullPlayerData || fullPlayerData.length === 0) return [];
  const leagueGroups = _.groupBy(fullPlayerData, "league");
  return Object.entries(leagueGroups)
    .map(([league, players]) => {
      const validPlayersInLeague = players.filter(
        (p) =>
          p &&
          typeof p.age === "number" &&
          p.age >= 18 &&
          p.age <= 45 &&
          typeof p.scaled_value === "number" &&
          !isNaN(p.scaled_value)
      );
      if (validPlayersInLeague.length === 0) return { league, agingCurve: [] };
      const ageGroups = _.groupBy(validPlayersInLeague, "age");
      const agingCurve = Object.entries(ageGroups)
        .map(([ageStr, agePlayers]) => {
          const age = parseInt(ageStr);
          const avgValue = _.meanBy(agePlayers, "scaled_value");
          const totalOriginalPlayers = players.filter(
            (p) => p.age === age
          ).length;
          return {
            age,
            avgValue,
            count: totalOriginalPlayers,
            validCount: agePlayers.length,
          };
        })
        .sort((a, b) => a.age - b.age);
      return { league, agingCurve };
    })
    .filter((leagueData) => leagueData.agingCurve.length > 0);
};

// --- Filtering/Ranking ---
const filterAndLimitPlayers = (playerData, metricKey, limit, filters) => {
  if (!playerData || !Array.isArray(playerData)) return [];
  let validData = playerData.filter(
    (p) =>
      p &&
      p.id != null &&
      typeof p[metricKey] === "number" &&
      !isNaN(p[metricKey])
  );
  if (filters) {
    // Apply filters
    if (filters.sport?.length > 0)
      validData = validData.filter(
        (p) => p.sport && filters.sport.includes(p.sport)
      );
    if (filters.league?.length > 0)
      validData = validData.filter(
        (p) => p.league && filters.league.includes(p.league)
      );
    if (filters.position?.length > 0)
      validData = validData.filter(
        (p) => p.position && filters.position.includes(p.position)
      );
  }
  validData.sort((a, b) => b[metricKey] - a[metricKey]); // Sort descending
  return limit ? validData.slice(0, limit) : validData; // Apply limit (0 or undefined = all)
};

export const getTopPlayersByPQI = (pqiData, limit, filters) => {
  // Use pqi_selected as the metric key
  return filterAndLimitPlayers(
    pqiData,
    "pqi_selected",
    limit === 0 ? undefined : limit,
    filters
  );
};

export const getTopPlayersByCQI = (cqiData, limit, filters) => {
  // Use cqi_selected as the metric key
  return filterAndLimitPlayers(
    cqiData,
    "cqi_selected",
    limit === 0 ? undefined : limit,
    filters
  );
};

// --- Corrected joinPlayerMetrics ---
export const joinPlayerMetrics = (pqiData, cqiData, limit, filters) => {
  if (
    !pqiData ||
    !cqiData ||
    !Array.isArray(pqiData) ||
    !Array.isArray(cqiData)
  )
    return [];

  // Use already processed & validated data if coming from processApiData
  // Otherwise, ensure numeric scores here if needed (assuming they are numbers now)
  const pqiMap = new Map(pqiData.map((p) => [p.id, p]));

  let joined = cqiData
    .map((cqiPlayer) => {
      const pqiPlayer = pqiMap.get(cqiPlayer.id);
      // Ensure both players exist and have the necessary *selected* scores
      if (
        !pqiPlayer ||
        typeof pqiPlayer.pqi_selected !== "number" ||
        typeof cqiPlayer.cqi_selected !== "number"
      ) {
        return null;
      }

      const pqiScore = pqiPlayer.pqi_selected;
      const cqiScore = cqiPlayer.cqi_selected;

      // Construct the joined object using the correct field names consistently
      return {
        ...pqiPlayer, // Base info from PQI player (includes id, name, league, position, pqi_selected, selected_tier etc.)
        // Add CQI specific fields explicitly
        cqi_selected: cqiScore,
        cqi_tier: cqiPlayer.selected_tier, // Use tier from processed CQI
        // Calculate index_difference using the selected scores
        index_difference: cqiScore - pqiScore,
        // Merge/prioritize other common fields if necessary (e.g., is_active)
        is_active: pqiPlayer.is_active ?? cqiPlayer.is_active ?? false,
      };
    })
    .filter((p) => p !== null); // Remove entries where matching failed

  // Apply dashboard filters AFTER joining
  if (filters) {
    if (filters.sport?.length > 0)
      joined = joined.filter((p) => p.sport && filters.sport.includes(p.sport));
    if (filters.league?.length > 0)
      joined = joined.filter(
        (p) => p.league && filters.league.includes(p.league)
      );
    if (filters.position?.length > 0)
      joined = joined.filter(
        (p) => p.position && filters.position.includes(p.position)
      );
  }

  // Sort by absolute difference (optional, based on desired default sort)
  // joined.sort((a, b) => Math.abs(b.index_difference) - Math.abs(a.index_difference));

  // Sort primarily by PQI, then CQI as a fallback default for comparison view consistency?
  joined.sort((a, b) => {
    const pqiDiff = b.pqi_selected - a.pqi_selected;
    if (pqiDiff !== 0) return pqiDiff;
    return b.cqi_selected - a.cqi_selected; // Secondary sort by CQI
  });

  // Apply limit (0 or undefined means no limit)
  const effectiveLimit = limit === 0 ? undefined : limit;
  return effectiveLimit ? joined.slice(0, effectiveLimit) : joined;
};

export const getPlayersByPrimeDuration = (
  primesSplineData,
  pqiData,
  limit,
  filters
) => {
  if (
    !primesSplineData ||
    !pqiData ||
    !Array.isArray(primesSplineData) ||
    !Array.isArray(pqiData)
  )
    return [];
  const pqiMap = new Map(pqiData.map((p) => [p.id, p]));
  let joinedData = primesSplineData
    .map((prime) => {
      if (!prime || prime.id == null) return null;
      const playerInfo = pqiMap.get(prime.id);
      if (!playerInfo) return null;
      const primeDuration =
        typeof prime.prime_duration === "number" && !isNaN(prime.prime_duration)
          ? prime.prime_duration
          : 0;
      const startAge =
        typeof prime.start_age === "number" ? prime.start_age : null;
      const endAge = typeof prime.end_age === "number" ? prime.end_age : null;
      const peakAge =
        typeof prime.max_value_age === "number" ? prime.max_value_age : null;
      const pqiScore =
        typeof playerInfo.pqi_selected === "number"
          ? playerInfo.pqi_selected
          : null; // Use pqi_selected
      return {
        id: prime.id,
        player_name: playerInfo.player_name,
        sport: playerInfo.sport,
        league: playerInfo.league,
        position: playerInfo.position,
        prime_duration: primeDuration,
        start_age: startAge,
        end_age: endAge,
        max_value_age: peakAge,
        pqi_selected: pqiScore,
      };
    })
    .filter((item) => item !== null && item.prime_duration > 0);
  if (filters) {
    // Apply filters
    if (filters.sport?.length > 0)
      joinedData = joinedData.filter(
        (p) => p.sport && filters.sport.includes(p.sport)
      );
    if (filters.league?.length > 0)
      joinedData = joinedData.filter(
        (p) => p.league && filters.league.includes(p.league)
      );
    if (filters.position?.length > 0)
      joinedData = joinedData.filter(
        (p) => p.position && filters.position.includes(p.position)
      );
  }
  joinedData.sort((a, b) => b.prime_duration - a.prime_duration); // Sort
  const effectiveLimit = limit === 0 ? undefined : limit; // Handle limit
  return effectiveLimit ? joinedData.slice(0, effectiveLimit) : joinedData;
};

// --- Player Specific Getters ---
// Assume input data arrays (e.g., fullData, pqiData) have been processed already
export const getPlayerCareer = (playerId, fullData = []) =>
  fullData.filter((row) => row?.id === playerId);
export const getPlayerPQI = (playerId, pqiData = []) =>
  pqiData.find((row) => row?.id === playerId) || null;
export const getPlayerCQI = (playerId, cqiData = []) =>
  cqiData.find((row) => row?.id === playerId) || null;
export const getPlayerPrimeRaw = (playerId, primesRawData = []) =>
  primesRawData.find((row) => row?.id === playerId) || null;
export const getPlayerPrimeSpline = (playerId, primesSplineData = []) =>
  primesSplineData.find((row) => row?.id === playerId) || null;
export const getPlayerPredictions = (playerId, playerPredictions = []) =>
  playerPredictions.filter((row) => row?.id === playerId);

// --- Search ---
export const searchPlayersByName = (searchTerm, players = []) => {
  if (!searchTerm || searchTerm.length < 2 || !Array.isArray(players))
    return [];
  const lowerCaseSearchTerm = searchTerm.toLowerCase();
  // Ensure player_name exists before calling toLowerCase
  return players.filter((player) =>
    player?.player_name?.toLowerCase().includes(lowerCaseSearchTerm)
  );
};

// --- Utility Exports ---
export const getUniqueLeagues = (pqiData = []) => [
  ...new Set(pqiData.map((p) => p?.league).filter(Boolean)),
];
export const getUniquePositions = (pqiData = []) =>
  [...new Set(pqiData.map((p) => p?.position).filter(Boolean))].sort();
