// src/App.js - Updated with progressive loading and progress callback
import React, { useState, useEffect, useCallback } from "react"; // Removed lazy/Suspense for Dashboard
import {
  HashRouter as Router,
  Routes,
  Route,
  Navigate,
  useLocation,
} from "react-router-dom";
import ReactGA from "react-ga4";

// Eagerly load essential components
import LandingPage from "./components/LandingPage";
import DataErrorBoundary from "./components/DataErrorBoundary";
import AnalyticsContextInfo from "./components/AnalyticsContextInfo"; // Methodology info

// Import Dashboard normally since we need to pass it props directly
import Dashboard from "./components/Dashboard";
// const PosterDisplay = lazy(() => import("./components/PosterDisplay")); // Example if you add other lazy routes

// Utils & Styles
import { loadAllData } from "./utils/dataLoader"; // Use the modified loader
import "./styles/App.css"; // Ensure this contains loading indicator styles

// --- GA Tracking (Keep as is from your version) ---
function GAPageTracker() {
  const location = useLocation();
  useEffect(() => {
    // Using window flag to avoid reinitialization on HMR
    if (!window.gaInitialized) {
      const measurementId = "G-LRR8N75MLG"; // Replace with your actual Measurement ID
      if (measurementId && measurementId !== "YOUR_MEASUREMENT_ID") {
        // Basic check
        ReactGA.initialize(measurementId);
        window.gaInitialized = true; // Set flag
        console.log("GA Initialized with ID:", measurementId);
      } else {
        console.warn("GA Measurement ID not set or invalid.");
      }
    }
    // Send pageview on location change if initialized
    if (window.gaInitialized) {
      // HashRouter typically uses hash fragments, include them if needed
      const pagePath = location.pathname + location.search + location.hash;
      ReactGA.send({
        hitType: "pageview",
        page: pagePath,
        title: document.title,
      }); // Include title
      console.log(`GA Pageview Sent: ${pagePath}`);
    }
  }, [location]); // Re-run on location change

  return null; // Component does not render anything
}

// --- Global Loading Indicator Component ---
const GlobalLoadingIndicator = ({ loading, stage, progress }) => {
  // Only render if loading is true
  if (!loading) return null;
  return (
    <div className="global-loading-indicator">
      <div className="loading-progress" style={{ width: `${progress}%` }}></div>
      <div
        className="loading-message-minimal"
        title={`${stage} (${progress}%)`}
      >
        {/* Truncate long stage messages if needed */}
        {stage.length > 50 ? stage.substring(0, 47) + "..." : stage} ({progress}
        %)
      </div>
    </div>
  );
};

// --- Main App Component ---
function App() {
  const [data, setData] = useState(null); // Holds the final loaded data
  const [loadingState, setLoadingState] = useState({
    isLoading: true, // Overall loading status, true initially
    progress: 0,
    stage: "Initializing...",
  });
  const [loadError, setLoadError] = useState(null); // Store loading errors

  // Define the callback function for dataLoader progress updates
  // Use useCallback to ensure the function identity is stable
  const handleProgressUpdate = useCallback(({ stage, progress }) => {
    // Update the loading state based on progress info from dataLoader
    setLoadingState((prev) => ({
      isLoading: progress < 100 && !prev.error, // Keep loading until 100% unless error occurred
      progress: progress,
      stage: stage,
    }));
  }, []); // Empty dependency array means this function is created once

  // Effect to load data on initial mount
  useEffect(() => {
    let isMounted = true; // Flag to prevent state updates on unmounted component

    const fetchData = async () => {
      // Reset state before starting fetch
      setLoadingState({
        isLoading: true,
        progress: 0,
        stage: "Initializing...",
      });
      setLoadError(null);
      setData(null);

      try {
        // Call loadAllData and pass the progress update handler
        const loadedData = await loadAllData(handleProgressUpdate);

        // If component is still mounted after data loads, update state
        if (isMounted) {
          setData(loadedData);
          // Ensure final state reflects completion (loader might finish slightly before 100%)
          setLoadingState({
            isLoading: false,
            progress: 100,
            stage: "Complete",
          });
          console.log("Data loaded successfully in App.js");
        }
      } catch (err) {
        // If component is still mounted and an error occurred
        if (isMounted) {
          console.error("Error loading data in App.js:", err);
          setLoadError(
            `Failed to load core data: ${err.message}. Please check console and data files.`
          );
          // Update state to reflect error, stop loading
          setLoadingState({ isLoading: false, progress: 0, stage: "Error" });
        }
      }
    };

    fetchData(); // Execute the async function

    // Cleanup function: runs when the component unmounts
    return () => {
      isMounted = false; // Set flag to false to prevent state updates
    };
  }, [handleProgressUpdate]); // Re-run effect only if handleProgressUpdate changes (it won't due to useCallback)

  // --- Render Error State ---
  // Display a full-page error if data loading failed critically
  if (loadError) {
    return (
      <div className="error-container">
        <div className="error-title">Error Loading Application Data</div>
        <div className="error-details">{loadError}</div>
        <div className="troubleshooting">
          <p>Please try the following:</p>
          <ul>
            <li>Refresh the page.</li>
            <li>
              Ensure data files (e.g., CSVs in /public/data) are present and
              correctly formatted.
            </li>
            <li>
              Check the browser's console (F12) for more specific error
              messages.
            </li>
          </ul>
          <button
            onClick={() => window.location.reload()}
            className="reload-button"
          >
            Reload Page
          </button>
        </div>
      </div>
    );
  }

  // --- Render Main Application ---
  return (
    <Router>
      <div className="App">
        <GAPageTracker /> {/* Include GA tracking */}
        {/* Render the global loading indicator based on loading state */}
        <GlobalLoadingIndicator
          loading={loadingState.isLoading}
          stage={loadingState.stage}
          progress={loadingState.progress}
        />
        <Routes>
          {/* Landing Page - always available, pass loading status if needed */}
          <Route
            path="/"
            element={<LandingPage dataIsLoading={loadingState.isLoading} />}
          />

          {/* Methodology page - assumed to be static, renders immediately */}
          <Route
            path="/methodology"
            element={<AnalyticsContextInfo /* Pass props if needed */ />}
          />

          {/* Dashboard Route - Pass data and loading state */}
          {/* Wrap with Error Boundary for safety */}
          <Route
            path="/dashboard" // Or "/dashboard/*" if Dashboard has internal routes
            element={
              <DataErrorBoundary>
                {/* Dashboard is rendered eagerly, but handles its internal loading state */}
                <Dashboard
                  data={data} // Pass the loaded data (null initially)
                  isDataLoading={loadingState.isLoading} // Let Dashboard know if app is still loading
                  loadingStage={loadingState.stage} // Pass current stage message
                />
              </DataErrorBoundary>
            }
          />

          {/* Example for another potential lazy route (if needed later) */}
          {/* <Route
            path="/poster"
            element={
              <DataErrorBoundary>
                <Suspense fallback={<div>Loading Poster Code...</div>}>
                  <PosterDisplay />
                </Suspense>
              </DataErrorBoundary>
            }
          /> */}

          {/* Redirect any unknown paths to the landing page */}
          <Route path="*" element={<Navigate to="/" replace />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;
