// Helper function to normalize a player's values to 0-100 scale
const normalizePlayerValues = (data, valueKey) => {
  if (!data || data.length === 0) return [];

  const validValues = data
    .map((item) => item?.[valueKey]) // Safe access
    .filter((v) => typeof v === "number" && !isNaN(v));

  if (validValues.length === 0) {
    return data.map((item) => ({ ...item, normalizedValue: null }));
  }

  const minValue = Math.min(...validValues);
  const maxValue = Math.max(...validValues);

  const valueRange = maxValue - minValue;
  if (valueRange === 0) {
    // If all values are the same, map them to 50 or keep original? Let's map to 50.
    return data.map((item) => ({
      ...item,
      normalizedValue: typeof item?.[valueKey] === "number" ? 50 : null,
    }));
  }

  return data.map((item) => {
    const value = item?.[valueKey];
    if (typeof value === "number" && !isNaN(value)) {
      const normalizedValue = ((value - minValue) / valueRange) * 100;
      return { ...item, normalizedValue };
    }
    return { ...item, normalizedValue: null };
  });
};

const PlayerSummaryTable = ({ player, playerInfo }) => {
  if (!playerInfo) return null;

  // Use spline data to determine prime metrics
  const peakAge = playerInfo.primeSpline?.max_value_age || "N/A";
  const primeStart = playerInfo.primeSpline?.start_age || "N/A";
  const primeEnd = playerInfo.primeSpline?.end_age || "N/A";
  const primeDuration = playerInfo.primeSpline?.prime_duration || "N/A";

  return (
    <div className="player-summary">
      <h4 style={{ display: "flex", alignItems: "center" }}>
        Career Summary
        <ContextInfo
          title="Career Summary Table"
          description="This table shows key metrics about the player's career path using spline-based predicted data, including their prime years, peak age, and both PQI and CQI scores. PQI (Performance Quotient Index) focuses on prime years and peak performance, while CQI (Career Quality Index) evaluates overall career quality."
        />
      </h4>
      <table>
        <tbody>
          <tr>
            <td>Peak Age (Predicted):</td>
            <td>{peakAge}</td>
            <td>Prime Duration (Predicted):</td>
            <td>{primeDuration ? `${primeDuration} years` : "N/A"}</td>
          </tr>
          <tr>
            <td>Prime Period (Predicted):</td>
            <td colSpan="3">
              {primeStart !== "N/A" && primeEnd !== "N/A"
                ? `Ages ${primeStart} - ${primeEnd}`
                : "N/A"}
            </td>
          </tr>
          <tr>
            <td>PQI Score:</td>
            <td>
              {typeof playerInfo.pqiData?.pqi_selected === "number"
                ? playerInfo.pqiData.pqi_selected.toFixed(2)
                : "N/A"}
            </td>
            <td>PQI Tier:</td>
            <td
              style={{
                color:
                  tierColorMapping[playerInfo.pqiData?.selected_tier] || "#333",
                fontWeight: "bold",
              }}
            >
              {playerInfo.pqiData?.selected_tier || "N/A"}
            </td>
          </tr>
          <tr>
            <td>CQI Score:</td>
            <td>
              {typeof playerInfo.cqiData?.cqi_selected === "number"
                ? playerInfo.cqiData.cqi_selected.toFixed(2)
                : "N/A"}
            </td>
            <td>CQI Tier:</td>
            <td
              style={{
                color:
                  tierColorMapping[playerInfo.cqiData?.selected_tier] || "#333",
                fontWeight: "bold",
              }}
            >
              {playerInfo.cqiData?.selected_tier || "N/A"}
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  );
};

