const PlayerComparison = ({
  data,
  positions,
  leagues,
  useLogos = false,
  toggleLogos = () => {},
}) => {
  const [searchTerm, setSearchTerm] = useState("");
  const [searchResults, setSearchResults] = useState([]);
  const [selectedPlayers, setSelectedPlayers] = useState([]);
  const [playerData, setPlayerData] = useState({});
  const [viewMode, setViewMode] = useState("individual");
  const [showChartPopup, setShowChartPopup] = useState(false);

  // Memoize unique players from pqiData for search stability
  const uniquePlayers = useMemo(() => {
    if (!data?.pqiData) return [];
    // Use a Map to ensure uniqueness based on player id
    const playerMap = new Map();
    data.pqiData.forEach((player) => {
      if (player && player.id) {
        // Ensure player and id exist
        playerMap.set(player.id, player);
      }
    });
    return Array.from(playerMap.values());
  }, [data?.pqiData]);

  // Updated Search function using memoized unique players
  useEffect(() => {
    if (searchTerm.length >= 2 && uniquePlayers.length > 0) {
      const results = searchPlayersByName(searchTerm, uniquePlayers); // Search the unique list
      setSearchResults(results.slice(0, 10)); // Limit results
    } else {
      setSearchResults([]);
    }
  }, [searchTerm, uniquePlayers]); // Depend on uniquePlayers

  // Updated data loading logic to be more robust
  const loadPlayerData = useCallback(
    async (playersToLoad) => {
      if (
        !data ||
        !data.fullData ||
        !data.pqiData ||
        !data.cqiData ||
        !data.primesRawData ||
        !data.primesSplineData
      ) {
        console.warn("Required data sources missing for player comparison.");
        return {}; // Return empty object if essential data is missing
      }

      const newPlayerData = { ...playerData }; // Start with existing loaded data

      for (const player of playersToLoad) {
        // Check if data exists and isn't marked as error
        if (newPlayerData[player.id] && !newPlayerData[player.id].error)
          continue;

        try {
          // Reset error state if retrying
          if (newPlayerData[player.id]?.error) {
            delete newPlayerData[player.id].error;
          }

          const careerData = getPlayerCareer(player.id, data.fullData) || [];

          // Handle missing playerPredictions gracefully
          let predictedData = [];
          if (data.playerPredictions && Array.isArray(data.playerPredictions)) {
            predictedData =
              getPlayerPredictions(player.id, data.playerPredictions) || [];
          }

          // Log data before normalization for debugging
          console.log(
            `Player ${player.player_name}: Found ${careerData.length} career data points and ${predictedData.length} prediction points`
          );

          const normalizedActualData = normalizePlayerValues(
            careerData,
            "player_value"
          );
          const normalizedPredictedData = normalizePlayerValues(
            predictedData,
            "predicted_value"
          );

          const pqiDataResult = getPlayerPQI(player.id, data.pqiData);
          const cqiDataResult = getPlayerCQI(player.id, data.cqiData);
          const primeRawResult = getPlayerPrimeRaw(
            player.id,
            data.primesRawData
          );
          const primeSplineResult = getPlayerPrimeSpline(
            player.id,
            data.primesSplineData
          );

          // Enhanced logging for prime data
          if (primeRawResult) {
            console.log(`Player ${player.player_name} raw prime data:`, {
              start: primeRawResult.start_age,
              peak: primeRawResult.max_value_age,
              end: primeRawResult.end_age,
            });
          }

          if (primeSplineResult) {
            console.log(`Player ${player.player_name} spline prime data:`, {
              start: primeSplineResult.start_age,
              peak: primeSplineResult.max_value_age,
              end: primeSplineResult.end_age,
            });
          }

          // Basic check if essential data was found
          if (!pqiDataResult && !cqiDataResult) {
            console.warn(
              `No PQI or CQI data found for player ${player.player_name} (ID: ${player.id}). Skipping detailed load.`
            );
            newPlayerData[player.id] = {
              error: true,
              message: "Core data missing",
            }; // Mark as error
            continue; // Move to next player
          }

          newPlayerData[player.id] = {
            careerData: normalizedActualData,
            predictedData: normalizedPredictedData,
            pqiData: pqiDataResult,
            cqiData: cqiDataResult,
            primeRaw: primeRawResult,
            primeSpline: primeSplineResult,
          };
        } catch (err) {
          console.error(
            `Error loading data for player ${player.player_name} (ID: ${player.id}):`,
            err
          );
          // Add a placeholder to indicate loading failed for this player
          newPlayerData[player.id] = {
            error: true,
            message: err.message || "Unknown error",
          };
        }
      }
      setPlayerData(newPlayerData);
    },
    [data, playerData]
  ); // Dependencies: base data and existing loaded data

  // Trigger data loading when selected players change
  useEffect(() => {
    const playersToLoad = selectedPlayers.filter(
      (p) => !playerData[p.id] || playerData[p.id]?.error
    ); // Load if not present or if errored previously
    if (playersToLoad.length > 0) {
      loadPlayerData(playersToLoad);
    }
  }, [selectedPlayers, loadPlayerData, playerData]);

  const handlePlayerSelect = (player) => {
    // Prevent adding duplicates
    if (selectedPlayers.some((p) => p.id === player.id)) {
      setSearchTerm("");
      setSearchResults([]);
      return;
    }
    if (selectedPlayers.length < 5) {
      setSelectedPlayers([...selectedPlayers, player]);
      setSearchTerm("");
      setSearchResults([]);
    } else {
      // Optionally notify user that max players reached
      console.warn("Maximum of 5 players already selected.");
    }
  };

  const handlePlayerRemove = (playerId) => {
    setSelectedPlayers(
      selectedPlayers.filter((player) => player.id !== playerId)
    );
    // Keep player data in state in case they are re-added, no need to remove here
    // const updatedPlayerData = { ...playerData };
    // delete updatedPlayerData[playerId];
    // setPlayerData(updatedPlayerData);
  };

  // Render function for individual player cards
  const renderPlayerCard = (player) => {
    const playerInfo = playerData[player.id];

    // Handle loading state
    if (!playerInfo) {
      return (
        <div className="player-card loading">
          Loading {player.player_name}...
        </div>
      );
    }
    // Handle error state
    if (playerInfo.error) {
      return (
        <div className="player-card error">
          Error loading {player.player_name}:{" "}
          {playerInfo.message || "Data unavailable"}{" "}
          <button onClick={() => loadPlayerData([player])}>Retry</button>
        </div>
      );
    }

    return (
      <div className="player-card" key={player.id}>
        <div
          className="player-header"
          style={{
            backgroundColor: leagueColorMapping[player.league] || "#6D1945",
          }}
        >
          <h3>{player.player_name}</h3>
          <button onClick={() => handlePlayerRemove(player.id)}>Remove</button>
        </div>

        <div className="player-info-grid">
          <div className="info-item">
            <span className="label">Sport/League:</span>
            <span className="value">
              {useLogos ? (
                <span
                  style={{ display: "flex", alignItems: "center", gap: "5px" }}
                >
                  <SportDisplay
                    sport={player.sport}
                    useLogos={useLogos}
                    size="medium"
                  />
                  <LeagueDisplay
                    league={player.league}
                    useLogos={useLogos}
                    size="medium"
                  />
                </span>
              ) : (
                <span>
                  <SportDisplay sport={player.sport} /> /{" "}
                  <LeagueDisplay league={player.league} />
                </span>
              )}
            </span>
          </div>
          <div className="info-item">
            <span className="label">Position:</span>
            <span className="value">
              <PositionDisplay
                position={player.position}
                league={player.league}
                sport={player.sport}
                showFull={true}
              />
            </span>
          </div>
          <div className="info-item">
            <span className="label">Career Seasons:</span>
            <span className="value">
              {playerInfo.pqiData?.career_seasons ||
                playerInfo.cqiData?.career_seasons ||
                "N/A"}
            </span>
          </div>
          <div className="info-item">
            <span className="label">Prime Seasons (Pred):</span>
            <span className="value">
              {playerInfo.primeSpline?.prime_duration || "N/A"}
            </span>
          </div>
          <div className="info-item">
            <span className="label">PQI Score:</span>
            <span className="value">
              {typeof playerInfo.pqiData?.pqi_selected === "number"
                ? playerInfo.pqiData.pqi_selected.toFixed(2)
                : "N/A"}
            </span>
          </div>
          <div className="info-item">
            <span className="label">CQI Score:</span>
            <span className="value">
              {typeof playerInfo.cqiData?.cqi_selected === "number"
                ? playerInfo.cqiData.cqi_selected.toFixed(2)
                : "N/A"}
            </span>
          </div>
        </div>

        {/* Render the components */}
        <PerformanceMetrics player={player} playerInfo={playerInfo} />
        <PlayerSummaryTable player={player} playerInfo={playerInfo} />
        <PlayerChartSection
          player={player}
          playerInfo={playerInfo}
          useLogos={useLogos}
        />
      </div>
    );
  };

  // Helper function to prepare chart data with all players' predicted values
  const prepareChartData = useCallback((players, playerDataMap) => {
    const allAges = new Set();
    players.forEach((player) => {
      const playerInfo = playerDataMap[player.id];
      // Only include players without errors and with data
      if (playerInfo && !playerInfo.error && playerInfo.predictedData) {
        playerInfo.predictedData.forEach((data) => {
          if (data && typeof data.age === "number") {
            allAges.add(data.age);
          }
        });
      }
    });

    const sortedAges = Array.from(allAges).sort((a, b) => a - b);
    if (sortedAges.length === 0) return []; // Return empty if no valid ages found

    return sortedAges.map((age) => {
      const dataPoint = { age };
      players.forEach((player) => {
        const playerInfo = playerDataMap[player.id];
        let value = null;
        // Only include player value if no error and data exists
        if (playerInfo && !playerInfo.error && playerInfo.predictedData) {
          const ageData = playerInfo.predictedData.find((d) => d?.age === age);
          value = ageData ? ageData.normalizedValue : null;
        }
        dataPoint[`player_${player.id}`] = value;
      });
      return dataPoint;
    });
  }, []); // Empty dependency array - function itself doesn't depend on external state changing

  const renderOverlayView = () => {
    if (selectedPlayers.length === 0) return null;

    const playerColors = [
      "#6D1945",
      "#4caf50",
      "#ff9800",
      "#2196f3",
      "#9c27b0",
    ];
    const combinedData = prepareChartData(selectedPlayers, playerData); // Use the memoized helper

    // Tooltip formatter with safety check
    const tooltipFormatter = (value) => {
      if (typeof value === "number") {
        return value.toFixed(1);
      }
      return "N/A";
    };

    if (combinedData.length === 0) {
      return (
        <div className="no-chart-data">
          No comparable predicted data available for selected players (check for
          loading errors).
        </div>
      );
    }

    return (
      <div className="overlay-view">
        <h3 style={{ display: "flex", alignItems: "center" }}>
          Predicted Performance Comparison
          <ContextInfo
            title="Predicted Performance Comparison Chart"
            description="This chart overlays the normalized predicted performance curves of all selected players, allowing for direct comparison of their projected career trajectories and peak ages."
          />
        </h3>
        <ResponsiveContainer width="100%" height={500}>
          <LineChart
            data={combinedData}
            margin={{ top: 20, right: 30, left: 20, bottom: 25 }}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis
              dataKey="age"
              type="number"
              domain={["dataMin", "dataMax"]}
              label={{ value: "Age", position: "insideBottom", offset: -5 }}
              tick={{ fontSize: 12 }}
            />
            <YAxis
              domain={[0, 100]}
              label={{
                value: "Predicted Performance (%)",
                angle: -90,
                position: "insideLeft",
                dx: -15,
                style: { textAnchor: "middle" },
              }}
            />
            <Tooltip
              content={({ active, payload, label }) => {
                if (
                  !active ||
                  !payload ||
                  payload.length === 0 ||
                  label === undefined ||
                  label === null
                )
                  return null;

                const playerValues = payload
                  .map((entry) => {
                    const playerId = entry.dataKey?.replace("player_", "");
                    if (!playerId) return null;
                    const player = selectedPlayers.find(
                      (p) => p.id === playerId
                    );
                    // Check if value exists and is a number
                    const valueExists =
                      entry.value !== null &&
                      entry.value !== undefined &&
                      typeof entry.value === "number";
                    return player && valueExists
                      ? { player, value: entry.value, color: entry.color }
                      : null;
                  })
                  .filter(Boolean); // Remove null entries

                return (
                  <div className="custom-tooltip">
                    <p style={{ margin: "0 0 8px", fontWeight: "bold" }}>
                      Age: {label}
                    </p>
                    {playerValues.length > 0 ? (
                      playerValues.map((item, i) => (
                        <div
                          key={`tooltip-player-${item.player.id}`}
                          style={{
                            marginBottom: "6px",
                            display: "flex",
                            alignItems: "center",
                            gap: "8px",
                          }}
                        >
                          <span
                            style={{
                              width: "10px",
                              height: "10px",
                              backgroundColor: item.color,
                              display: "inline-block",
                              borderRadius: "50%",
                            }}
                          ></span>
                          <span
                            style={{ fontWeight: "bold", color: item.color }}
                          >
                            {item.player.player_name}:
                          </span>
                          {/* Apply formatter here */}
                          <span>{tooltipFormatter(item.value)}</span>
                          {useLogos && (
                            <div
                              style={{
                                display: "flex",
                                alignItems: "center",
                                gap: "4px",
                              }}
                            >
                              <SportDisplay
                                sport={item.player.sport}
                                useLogos={true}
                                size="small"
                              />
                              <LeagueDisplay
                                league={item.player.league}
                                useLogos={true}
                                size="small"
                              />
                            </div>
                          )}
                        </div>
                      ))
                    ) : (
                      <p style={{ fontStyle: "italic", color: "#666" }}>
                        No data available at this age
                      </p>
                    )}
                  </div>
                );
              }}
            />
            <Legend
              formatter={(value, entry) => {
                const playerId = entry.dataKey?.replace("player_", "");
                if (!playerId) return value; // Fallback
                const player = selectedPlayers.find((p) => p.id === playerId);
                if (!player) return value; // Fallback
                if (useLogos) {
                  return (
                    <span
                      style={{
                        display: "inline-flex",
                        alignItems: "center",
                        gap: "5px",
                        verticalAlign: "middle",
                      }}
                    >
                      {player.player_name}
                      <SportDisplay
                        sport={player.sport}
                        useLogos={true}
                        size="small"
                      />
                      <LeagueDisplay
                        league={player.league}
                        useLogos={true}
                        size="small"
                      />
                    </span>
                  );
                }
                return player.player_name;
              }}
            />

            {selectedPlayers.map((player, index) => {
              // Only render line if player data exists and no error
              const playerInfo = playerData[player.id];
              if (!playerInfo || playerInfo.error) return null;

              return (
                <Line
                  key={`player-${player.id}`}
                  type="monotone"
                  dataKey={`player_${player.id}`}
                  name={player.player_name}
                  stroke={playerColors[index % playerColors.length]}
                  strokeWidth={2}
                  dot={false} // Simplify view
                  activeDot={{ r: 6 }}
                  connectNulls={true} // Important for players with different career lengths
                />
              );
            })}
          </LineChart>
        </ResponsiveContainer>
      </div>
    );
  };

  // renderTimelineView (remains the same)
  const renderTimelineView = () => {
    if (selectedPlayers.length === 0) return null;

    // Filter out players with errors or missing spline data
    const validPlayers = selectedPlayers.filter((player) => {
      const playerInfo = playerData[player.id];
      return playerInfo && !playerInfo.error && playerInfo.primeSpline;
    });

    if (validPlayers.length === 0) {
      return (
        <div className="no-chart-data">
          No valid timeline data available for selected players.
        </div>
      );
    }

    const maxPlayerAge = Math.max(
      38, // Minimum age extent
      ...validPlayers.map((player) => {
        const playerInfo = playerData[player.id];
        if (!playerInfo?.predictedData || playerInfo.predictedData.length === 0)
          return 0;
        return Math.max(...playerInfo.predictedData.map((d) => d?.age || 0));
      })
    );
    const minPlayerAge = 18; // Standard start

    return (
      <div className="timeline-view">
        <h3 style={{ display: "flex", alignItems: "center" }}>
          Predicted Career Prime Timeline
          <ContextInfo
            title="Predicted Career Prime Timeline"
            description="This visualization shows the predicted prime years of each selected player on a common timeline, based on the spline model. The colored sections represent the prime period, and stars mark predicted peak performance ages."
          />
        </h3>
        <div className="timeline-container">
          {validPlayers.map((player) => {
            // Iterate over valid players
            const primeSpline = playerData[player.id]?.primeSpline;
            // Double check required fields just in case
            if (
              !primeSpline ||
              typeof primeSpline.start_age !== "number" ||
              typeof primeSpline.end_age !== "number" ||
              typeof primeSpline.max_value_age !== "number"
            )
              return null;

            const primeDuration =
              primeSpline.prime_duration ||
              primeSpline.end_age - primeSpline.start_age + 1;

            return (
              <div className="timeline-row" key={player.id}>
                <div className="timeline-label">
                  <span className="player-name">{player.player_name}</span>
                  {useLogos && (
                    <span
                      style={{
                        display: "flex",
                        alignItems: "center",
                        gap: "5px",
                        marginLeft: "5px",
                      }}
                    >
                      <SportDisplay
                        sport={player.sport}
                        useLogos={true}
                        size="medium"
                      />
                      <LeagueDisplay
                        league={player.league}
                        useLogos={true}
                        size="medium"
                      />
                    </span>
                  )}
                </div>
                <div className="timeline-bar">
                  {Array.from(
                    { length: maxPlayerAge - minPlayerAge + 1 },
                    (_, i) => i + minPlayerAge
                  ).map((age) => {
                    const isPrime =
                      age >= primeSpline.start_age &&
                      age <= primeSpline.end_age;
                    const isPeak = age === primeSpline.max_value_age;

                    return (
                      <div
                        key={age}
                        className={`timeline-age ${isPrime ? "in-prime" : ""} ${
                          isPeak ? "peak-age" : ""
                        }`}
                        style={{
                          backgroundColor: isPrime
                            ? leagueColorMapping[player.league] || "#82ca9d"
                            : "transparent",
                        }}
                        title={`Age ${age}${isPrime ? " (In Prime)" : ""}${
                          isPeak ? " (Peak Age)" : ""
                        }`}
                      >
                        {isPeak && <div className="peak-marker">★</div>}
                        <div className="age-label">
                          {age % 2 === 0 ? age : ""}
                        </div>{" "}
                        {/* Show labels every 2 years */}
                      </div>
                    );
                  })}
                </div>
                <div className="timeline-info">
                  <span>Prime: {primeDuration} years</span>
                  <span>
                    ({primeSpline.start_age} - {primeSpline.end_age})
                  </span>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    );
  };

  // renderMetricsView (add checks for player errors)
  const renderMetricsView = () => {
    if (selectedPlayers.length === 0) return null;

    const metricsData = selectedPlayers
      .map((player) => {
        const playerInfo = playerData[player.id];
        // Skip players with errors or missing data
        if (
          !playerInfo ||
          playerInfo.error ||
          !playerInfo.pqiData ||
          !playerInfo.cqiData
        )
          return null;

        const pqiScore = playerInfo.pqiData?.pqi_selected ?? 0; // Use ?? 0 for default
        const cqiScore = playerInfo.cqiData?.cqi_selected ?? 0;
        const difference =
          typeof cqiScore === "number" && typeof pqiScore === "number"
            ? cqiScore - pqiScore
            : 0;

        return {
          id: player.id,
          name: player.player_name,
          league: player.league,
          sport: player.sport,
          position: player.position,
          pqiScore,
          cqiScore,
          difference,
          pqiTier: playerInfo.pqiData?.selected_tier || "N/A",
          cqiTier: playerInfo.cqiData?.selected_tier || "N/A",
          careerGames:
            playerInfo.pqiData?.career_games ||
            playerInfo.cqiData?.career_games ||
            0,
          primeDuration:
            playerInfo.primeSpline?.prime_duration ||
            playerInfo.pqiData?.prime_seasons ||
            0, // Prefer spline prime duration
          primePercentage: playerInfo.pqiData?.prime_density || 0,
        };
      })
      .filter((p) => p !== null); // Filter out the null entries

    if (metricsData.length === 0) {
      return (
        <div className="no-chart-data">
          No valid metrics data available for selected players.
        </div>
      );
    }

    const sortedMetrics = [...metricsData].sort(
      (a, b) => Math.abs(b.difference) - Math.abs(a.difference)
    );

    // Tooltip formatter with safety check
    const tooltipFormatter = (value, name) => {
      const label = name === "pqiScore" ? "PQI Score" : "CQI Score";
      if (typeof value === "number") {
        return [value.toFixed(2), label];
      }
      return ["N/A", label];
    };

    return (
      <div className="metrics-view">
        <h3 style={{ display: "flex", alignItems: "center" }}>
          PQI vs CQI Comparison
          <ContextInfo
            title="Performance vs Career Metrics"
            description="This view compares each player's Performance Quotient Index (PQI) and Career Quality Index (CQI) scores. PQI focuses on peak performance and prime years, while CQI evaluates career-wide quality including longevity and accumulation."
          />
        </h3>

        <div className="metrics-chart-container">
          <ResponsiveContainer
            width="100%"
            height={Math.max(200, metricsData.length * 50)}
          >
            <BarChart
              data={metricsData}
              layout="vertical"
              margin={{ top: 20, right: 30, left: 120, bottom: 20 }}
            >
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis type="number" domain={[0, 100]} />
              <YAxis
                dataKey="name"
                type="category"
                width={120}
                tick={(props) => {
                  const { x, y, payload } = props;
                  const player = metricsData.find(
                    (p) => p.name === payload.value
                  );
                  if (!player) return null; // Handle case where player might not be found
                  return (
                    <g transform={`translate(${x},${y})`}>
                      <text
                        x={-5}
                        y={0}
                        dy={4}
                        textAnchor="end"
                        fill="#666"
                        fontSize="12"
                      >
                        {payload.value}
                      </text>
                      {useLogos && (
                        <foreignObject
                          width="110"
                          height="24"
                          x={-110 - 10}
                          y={-12}
                        >
                          <div
                            style={{
                              display: "flex",
                              alignItems: "center",
                              justifyContent: "flex-end",
                              gap: "5px",
                              height: "100%",
                            }}
                          >
                            <SportDisplay
                              sport={player.sport}
                              useLogos={true}
                              size="small"
                            />
                            <LeagueDisplay
                              league={player.league}
                              useLogos={true}
                              size="small"
                            />
                          </div>
                        </foreignObject>
                      )}
                    </g>
                  );
                }}
              />
              <Tooltip formatter={tooltipFormatter} />
              <Legend />
              <Bar
                dataKey="pqiScore"
                name="PQI Score"
                fill="#6D1945"
                barSize={20}
              />
              <Bar
                dataKey="cqiScore"
                name="CQI Score"
                fill="#4caf50"
                barSize={20}
              />
            </BarChart>
          </ResponsiveContainer>
        </div>

        <div className="metrics-explanation">
          <h4>What Explains the Differences?</h4>
          <div className="explanation-content">
            <div className="explanation-text">
              <p>
                <strong>Performance Quotient Index (PQI)</strong> measures the
                quality and impact of an athlete's peak performance years. It's
                calculated using:
              </p>
              <ul>
                <li>
                  Prime seasons duration (weighted by logarithmic scaling)
                </li>
                <li>Average performance value during prime years</li>
                <li>Peak performance value (with 30% bonus weight)</li>
                <li>Consistency during prime years</li>
              </ul>
              <p>
                <strong>Career Quality Index (CQI)</strong> evaluates an
                athlete's entire career contribution. It considers:
              </p>
              <ul>
                <li>Total career seasons (weighted by logarithmic scaling)</li>
                <li>Average performance value across all seasons</li>
                <li>Career peak value (with only 10% bonus weight)</li>
                <li>Overall career consistency</li>
              </ul>
              <p>
                Athletes with significantly higher <strong>PQI than CQI</strong>{" "}
                typically had exceptional peak performance but shorter careers.
              </p>
              <p>
                Athletes with significantly higher <strong>CQI than PQI</strong>{" "}
                typically had longer careers with consistent performance,
                accumulating value over time.
              </p>
            </div>
            <div className="players-summary">
              <h4>Player Career Metrics</h4>
              <table>
                <thead>
                  <tr>
                    <th>Player</th>
                    <th>Prime %</th>
                    <th>Prime Seasons</th>
                    <th>Career Games</th>
                    <th>Index Diff</th>
                  </tr>
                </thead>
                <tbody>
                  {sortedMetrics.map((player) => (
                    <tr key={player.id}>
                      <td>
                        {player.name}
                        {useLogos && (
                          <span
                            style={{
                              marginLeft: "8px",
                              display: "inline-flex",
                              alignItems: "center",
                              gap: "5px",
                              verticalAlign: "middle",
                            }}
                          >
                            <SportDisplay
                              sport={player.sport}
                              useLogos={true}
                              size="small"
                            />
                            <LeagueDisplay
                              league={player.league}
                              useLogos={true}
                              size="small"
                            />
                          </span>
                        )}
                      </td>
                      <td>
                        {typeof player.primePercentage === "number"
                          ? `${player.primePercentage.toFixed(1)}%`
                          : "N/A"}
                      </td>
                      <td>{player.primeDuration || "N/A"}</td>
                      <td>
                        {typeof player.careerGames === "number"
                          ? Math.round(player.careerGames)
                          : "N/A"}
                      </td>
                      <td
                        className={
                          player.difference < 0 ? "negative" : "positive"
                        }
                      >
                        {typeof player.difference === "number"
                          ? player.difference > 0
                            ? `+${player.difference.toFixed(1)} CQI`
                            : `${player.difference.toFixed(1)} CQI`
                          : "N/A"}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    );
  };

  // ChartsPopup component definition
  const ChartsPopup = ({
    showChartPopup,
    setShowChartPopup,
    selectedPlayers,
    playerData,
    useLogos,
  }) => {
    if (!showChartPopup) return null;

    const handleOverlayClick = (e) => {
      if (e.target === e.currentTarget) {
        setShowChartPopup(false);
      }
    };

    const handleContainerClick = (e) => {
      e.stopPropagation();
    };

    // Tooltip formatter with safety check
    const tooltipFormatter = (value) => {
      if (typeof value === "number") {
        return value.toFixed(1);
      }
      return "N/A";
    };

    // Reusable Chart Component for Popup
    const PopupPlayerChart = ({ player, playerInfo, chartType, useLogos }) => {
      const dataToPlot =
        chartType === "actual"
          ? playerInfo?.careerData
          : playerInfo?.predictedData;
      const primeInfo =
        chartType === "actual" ? playerInfo?.primeRaw : playerInfo?.primeSpline;
      const chartName = chartType === "actual" ? "Actual" : "Predicted";
      const lineColor =
        leagueColorMapping[player.league] ||
        (chartType === "actual" ? "#8884d8" : "#82ca9d");
      const hasData =
        dataToPlot &&
        dataToPlot.length > 0 &&
        dataToPlot.some((d) => d.normalizedValue !== null);

      return (
        <div className="comparison-chart-container">
          <div className="chart-player-name" style={{ color: lineColor }}>
            {player.player_name}
            {useLogos && (
              <span
                style={{
                  marginLeft: "5px",
                  display: "inline-flex",
                  alignItems: "center",
                  gap: "5px",
                  verticalAlign: "middle",
                }}
              >
                <SportDisplay
                  sport={player.sport}
                  useLogos={true}
                  size="small"
                />
                <LeagueDisplay
                  league={player.league}
                  useLogos={true}
                  size="small"
                />
              </span>
            )}
          </div>
          {hasData ? (
            <ResponsiveContainer width="100%" height={220}>
              <LineChart
                data={dataToPlot.filter(
                  (d) => d?.age !== undefined && d?.normalizedValue !== null
                )}
                margin={{ top: 5, right: 5, bottom: 20, left: 5 }}
              >
                <CartesianGrid strokeDasharray="3 3" stroke="#eee" />
                <XAxis
                  dataKey="age"
                  type="number"
                  domain={["auto", "auto"]}
                  tickCount={5}
                  label={{
                    value: "Age",
                    position: "insideBottom",
                    offset: -5,
                    fontSize: 10,
                  }}
                />
                <YAxis domain={[0, 100]} tickCount={5} />
                <Tooltip formatter={tooltipFormatter} />
                {primeInfo &&
                  typeof primeInfo.start_age === "number" &&
                  typeof primeInfo.end_age === "number" && (
                    <ReferenceArea
                      x1={primeInfo.start_age}
                      x2={primeInfo.end_age}
                      fill={lineColor}
                      fillOpacity={0.2}
                    />
                  )}
                {primeInfo && typeof primeInfo.max_value_age === "number" && (
                  <ReferenceLine
                    x={primeInfo.max_value_age}
                    stroke={lineColor}
                    strokeDasharray="3 3"
                  />
                )}
                <Line
                  type="monotone"
                  dataKey="normalizedValue"
                  stroke={lineColor}
                  activeDot={{ r: 4 }}
                  dot={false}
                  isAnimationActive={false}
                  name={chartName}
                  connectNulls
                />
              </LineChart>
            </ResponsiveContainer>
          ) : (
            <div className="no-chart-data-small">No {chartType} data</div>
          )}
        </div>
      );
    };

    return (
      <div className="charts-popup-overlay" onClick={handleOverlayClick}>
        <div className="charts-popup-container" onClick={handleContainerClick}>
          <div className="charts-popup-header">
            <h3>Player Performance Charts</h3>
            <button
              className="close-button"
              onClick={() => setShowChartPopup(false)}
            >
              ×
            </button>
          </div>
          <div className="charts-popup-content">
            {selectedPlayers.map((player) => {
              const playerInfo = playerData[player.id];
              if (!playerInfo || playerInfo.error) {
                // Optionally render an error state for this player in the popup
                return (
                  <div key={player.id} className="popup-player-error">
                    Could not load charts for {player.player_name}.
                  </div>
                );
              }
              return (
                <div key={player.id} className="popup-player-charts">
                  {/* <h5 className="popup-player-title">{player.player_name}</h5> */}
                  <div className="chart-comparison-row">
                    <PopupPlayerChart
                      player={player}
                      playerInfo={playerInfo}
                      chartType="actual"
                      useLogos={useLogos}
                    />
                    <PopupPlayerChart
                      player={player}
                      playerInfo={playerInfo}
                      chartType="predicted"
                      useLogos={useLogos}
                    />
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    );
  };

  return (
    <div className="player-comparison full-width-fix">
      <div className="section-header">
        <h2 style={{ display: "flex", alignItems: "center" }}>
          Player Comparison
          <ContextInfo
            title="Player Comparison Tool"
            description="This tool allows you to search for and compare up to 5 athletes across different sports. You can analyze their actual career trajectories, predicted performance curves, PQI and CQI metrics, and visualize their prime years on a common timeline."
          />
        </h2>
        <div className="view-toggles">
          <button
            className={viewMode === "individual" ? "active" : ""}
            onClick={() => setViewMode("individual")}
          >
            Individual
          </button>
          <button
            className={viewMode === "overlay" ? "active" : ""}
            onClick={() => setViewMode("overlay")}
            disabled={selectedPlayers.length < 1}
          >
            Overlay
          </button>{" "}
          {/* Enable overlay for 1+ */}
          <button
            className={viewMode === "timeline" ? "active" : ""}
            onClick={() => setViewMode("timeline")}
            disabled={selectedPlayers.length === 0}
          >
            Timeline
          </button>
          <button
            className={viewMode === "metrics" ? "active" : ""}
            onClick={() => setViewMode("metrics")}
            disabled={selectedPlayers.length === 0}
          >
            Metrics
          </button>
          {selectedPlayers.length > 1 && ( // Show button if at least 1 player selected
            <button
              className="compare-charts-button"
              onClick={() => setShowChartPopup(true)}
              title="View Detailed Charts Side-by-Side"
            >
              <ChartIcon />
              Charts
            </button>
          )}
        </div>
      </div>

      {/* Search and Selection Section */}
      <div className="search-section">
        <div className="search-container">
          <input
            type="text"
            placeholder="Search players..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
          {searchResults.length > 0 && (
            <div className="search-results">
              {searchResults.map((player) => (
                <div
                  key={`search-${player.id}`}
                  className="search-result-item"
                  onClick={() => handlePlayerSelect(player)}
                  title={`Add ${player.player_name}`}
                >
                  <div className="player-name-wrapper">
                    {player.player_name}
                    {useLogos && (
                      <span className="search-result-logos">
                        <span
                          style={{
                            display: "flex",
                            alignItems: "center",
                            gap: "5px",
                          }}
                        >
                          <SportDisplay
                            sport={player.sport}
                            useLogos={true}
                            size="small"
                          />
                          <LeagueDisplay
                            league={player.league}
                            useLogos={true}
                            size="small"
                          />
                        </span>
                      </span>
                    )}
                  </div>
                  <div className="player-details">
                    {!useLogos && (
                      <span>
                        <SportDisplay sport={player.sport} size="small" /> /{" "}
                        <LeagueDisplay league={player.league} size="small" />
                      </span>
                    )}
                    {" - "}
                    <PositionDisplay
                      position={player.position}
                      league={player.league}
                      sport={player.sport}
                      size="small"
                    />
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>

        <div className="selected-players-container">
          <div className="selected-count">
            Selected ({selectedPlayers.length}/5)
          </div>
          <div className="selected-list">
            {selectedPlayers.map((player) => (
              <div
                key={`selected-${player.id}`}
                className="selected-player-item"
              >
                {player.player_name}
                {useLogos && (
                  <span className="selected-player-logos">
                    <span
                      style={{
                        display: "flex",
                        alignItems: "center",
                        gap: "5px",
                      }}
                    >
                      <SportDisplay
                        sport={player.sport}
                        useLogos={true}
                        size="small"
                      />
                      <LeagueDisplay
                        league={player.league}
                        useLogos={true}
                        size="small"
                      />
                    </span>
                  </span>
                )}
                <button
                  onClick={() => handlePlayerRemove(player.id)}
                  title={`Remove ${player.player_name}`}
                >
                  ✕
                </button>
              </div>
            ))}
          </div>
        </div>
      </div>

      <div className="results-section">
        {selectedPlayers.length === 0 ? (
          <div className="no-players-selected">
            Search and select up to 5 players to compare.
          </div>
        ) : (
          <>
            {viewMode === "individual" && (
              <div className="individual-view">
                {selectedPlayers.map((player) => renderPlayerCard(player))}
              </div>
            )}
            {/* Ensure views render only if players exist */}
            {viewMode === "overlay" &&
              selectedPlayers.length >= 1 &&
              renderOverlayView()}
            {viewMode === "timeline" &&
              selectedPlayers.length >= 1 &&
              renderTimelineView()}
            {viewMode === "metrics" &&
              selectedPlayers.length >= 1 &&
              renderMetricsView()}
          </>
        )}
      </div>

      {/* Integrated Charts Popup */}
      <ChartsPopup
        showChartPopup={showChartPopup}
        setShowChartPopup={setShowChartPopup}
        selectedPlayers={selectedPlayers}
        playerData={playerData}
        useLogos={useLogos} // Pass useLogos down
      />
    </div>
  );
};

export default PlayerComparison;
