// src/components/AgingCurves.js
import React, { useState, useEffect, useMemo, useCallback } from "react";
import {
  ResponsiveContainer,
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  PieChart,
  Pie,
  Cell,
  BarChart,
  Bar,
  Text,
} from "recharts";
import { getAgingCurvesByLeague } from "../utils/dataProcessing";
import { leagueColorMapping } from "../utils/constants";
import EnhancedFilters from "./EnhancedFilters";
import ContextInfo from "./ContextInfo";
import LeagueDisplay from "./LeagueDisplay";
import SportDisplay from "./SportDisplay";
import _ from "lodash";
import "../styles/AgingCurves.css";
import "../styles/EnhancedFilters.css";

// Career Phase Pie Chart Component - Added selectedAge prop
const CareerPhasePieChart = ({ phaseData, useLogos = false, selectedAge }) => {
  // <-- Added selectedAge prop
  if (
    !phaseData ||
    phaseData.length === 0 ||
    phaseData.every((p) => p.value === 0)
  ) {
    return (
      <div
        className="career-phase-chart no-data"
        style={{
          paddingTop: "30px",
          height: "400px",
          textAlign: "center",
          color: "#666",
        }}
      >
        <h3
          style={{
            textAlign: "center",
            marginBottom: "25px",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          Career Phase Distribution (Age {selectedAge}){" "}
          {/* <-- Used selectedAge */}
          <ContextInfo
            title="Career Phase Distribution"
            description="This chart shows the distribution of athletes across different career phases at the selected age. Phases include Pre-Prime (before prime years), In Prime Pre-Peak (in prime but before peak age), At Peak (at the peak performance age), In Prime Post-Peak (in prime but after peak age), and Post-Prime (after prime years)."
          />
        </h3>
        No phase data available for this selection.
      </div>
    );
  }

  // Filter out phases with zero value to avoid cluttering the chart/legend
  const filteredPhaseData = phaseData.filter((item) => item.value > 0);

  // Use the color mapping directly from the passed data
  const normalizedData = filteredPhaseData.map((item) => ({
    ...item,
    // Ensure color exists, provide default if needed
    color: item.color || "#cccccc",
  }));

  // Custom render for pie chart labels - avoiding cutoff
  const renderCustomizedLabel = ({
    cx,
    cy,
    midAngle,
    innerRadius,
    outerRadius,
    percent,
  }) => {
    // Only show labels for segments that are large enough
    if (percent < 0.08) return null;

    const RADIAN = Math.PI / 180;
    // Adjust label radius for better positioning
    const radius = innerRadius + (outerRadius - innerRadius) * 0.6;
    const x = cx + radius * Math.cos(-midAngle * RADIAN);
    const y = cy + radius * Math.sin(-midAngle * RADIAN);

    return (
      <text
        x={x}
        y={y}
        fill="white"
        textAnchor={x > cx ? "start" : "end"}
        dominantBaseline="central"
        fontSize={11} // Slightly smaller font
        fontWeight="bold"
      >
        {`${(percent * 100).toFixed(0)}%`} {/* No decimal for cleaner look */}
      </text>
    );
  };

  // Correctly access totalSample from the first element IF it exists
  const totalSample =
    normalizedData.length > 0 ? normalizedData[0]?.totalSample : 0;

  return (
    <div
      className="career-phase-chart"
      style={{ paddingTop: "30px", height: "400px" }}
    >
      <h3
        style={{
          textAlign: "center",
          marginBottom: "10px",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        Career Phase Distribution (Age {selectedAge}){" "}
        {/* <-- Used selectedAge */}
        <ContextInfo
          title="Career Phase Distribution"
          description="This chart shows the distribution of athletes across different career phases at the selected age. Phases include Pre-Prime (before prime years), In Prime Pre-Peak (in prime but before peak age), At Peak (at the peak performance age), In Prime Post-Peak (in prime but after peak age), and Post-Prime (after prime years)."
        />
      </h3>

      <ResponsiveContainer width="100%" height={300}>
        <PieChart>
          <Pie
            data={normalizedData}
            cx="50%"
            cy="50%"
            labelLine={false}
            label={renderCustomizedLabel}
            outerRadius={110} // Slightly smaller radius
            innerRadius={40} // Add inner radius for donut effect
            fill="#8884d8"
            dataKey="value"
            nameKey="name"
          >
            {normalizedData.map((entry, index) => (
              <Cell
                key={`cell-${index}`}
                fill={entry.color}
                stroke={entry.color}
              /> // Add stroke for definition
            ))}
          </Pie>
          <Tooltip
            formatter={(value, name, props) => {
              const count = props.payload?.payload?.count || 0;
              return [
                `${parseFloat(value).toFixed(1)}% (${count} athletes)`,
                name,
              ];
            }}
          />
          <Legend
            layout="horizontal"
            verticalAlign="bottom"
            align="center"
            wrapperStyle={{ paddingTop: "15px", fontSize: "12px" }} // Adjust style
          />
        </PieChart>
      </ResponsiveContainer>

      {/* Additional stats display */}
      <div
        className="phase-stats"
        style={{
          display: "flex",
          justifyContent: "center",
          margin: "0 auto",
          width: "80%",
          paddingTop: "0px", // Reduced padding
        }}
      >
        <div style={{ textAlign: "center" }}>
          <div style={{ fontSize: "13px", color: "#666" }}>
            {/* Use selectedAge here */}
            Total Athletes at Age {selectedAge}:{" "}
            {totalSample?.toLocaleString() || "N/A"}
          </div>
        </div>
      </div>
    </div>
  );
};

// --- Helper Function to Safely Get Data for Filtering ---
const getSafeFilteredData = (
  data,
  selectedSports,
  selectedLeagues,
  selectedPositions
) => {
  if (!data || !Array.isArray(data.fullData)) {
    console.warn(
      "AgingCurves getSafeFilteredData: Invalid or missing fullData."
    );
    return [];
  }

  let filtered = [...data.fullData];

  try {
    if (selectedSports && selectedSports.length > 0) {
      filtered = filtered.filter(
        (item) => item?.sport && selectedSports.includes(item.sport)
      );
    }
    if (selectedLeagues && selectedLeagues.length > 0) {
      filtered = filtered.filter(
        (item) => item?.league && selectedLeagues.includes(item.league)
      );
    }
    if (selectedPositions && selectedPositions.length > 0) {
      filtered = filtered.filter(
        (item) => item?.position && selectedPositions.includes(item.position)
      );
    }
  } catch (error) {
    console.error("Error during filtering in AgingCurves:", error);
    return filtered; // Return partially filtered data on error
  }

  return filtered;
};

// Full AgingCurves component
const AgingCurves = ({
  data, // This 'data' object now comes from Dashboard's getCurrentData()
  leagues,
  selectedSports = [],
  selectedLeagues = [],
  selectedPositions = [],
  setSelectedSports = () => {},
  setSelectedLeagues = () => {},
  setSelectedPositions = () => {},
  useLogos = false,
  toggleLogos = () => {},
}) => {
  const [agingCurves, setAgingCurves] = useState([]);
  const [selectedAge, setSelectedAge] = useState(28);
  const [agePerformance, setAgePerformance] = useState([]);
  const [careerPhases, setCareerPhases] = useState([]);

  // Memoized calculation of filter options
  const allSports = useMemo(() => {
    if (!data?.pqiData) return [];
    return [...new Set(data.pqiData.map((player) => player.sport))].filter(
      Boolean
    );
  }, [data?.pqiData]);

  const allPositions = useMemo(() => {
    if (!data?.pqiData) return [];
    return [...new Set(data.pqiData.map((player) => player.position))].filter(
      Boolean
    );
  }, [data?.pqiData]);

  // --- calculateAgePerformance function: Improved robustness ---
  const calculateAgePerformance = useCallback((age, filteredData) => {
    if (!filteredData || filteredData.length === 0) {
      setAgePerformance([]);
      setCareerPhases([]);
      return;
    }

    // Get all players at the selected age
    const playersAtAge = filteredData.filter((player) => player?.age === age);

    // Calculate average performance by league
    const leagueGroups = _.groupBy(playersAtAge, "league");
    const leaguePerformance = Object.keys(leagueGroups)
      .map((league) => {
        const leaguePlayers = leagueGroups[league];
        // Ensure scaled_value exists and is a number before averaging
        const validValues = leaguePlayers
          .map((p) => p?.scaled_value)
          .filter((v) => typeof v === "number" && !isNaN(v));

        const avgValue = validValues.length > 0 ? _.mean(validValues) : NaN;

        return {
          league,
          avgValue,
          count: leaguePlayers.length,
        };
      })
      .filter((league) => league.count >= 5 && !isNaN(league.avgValue)) // Reduced minimum, ensure avgValue is valid
      .sort((a, b) => b.avgValue - a.avgValue);

    setAgePerformance(leaguePerformance);

    // Calculate career phases for selected age with proper checks
    const primeCounts = {
      prePrime: 0,
      inPrimePre: 0,
      atPeak: 0,
      inPrimePost: 0,
      postPrime: 0,
    };

    let totalValidPlayers = 0;
    playersAtAge.forEach((player) => {
      // Basic validation: Ensure player object and necessary fields exist
      if (!player || typeof player.age !== "number") return;

      totalValidPlayers++; // Count only players we can process

      const isPeak = player.is_peak_age === true;
      const inPrime = player.in_prime === true;
      const yearsFromPeak =
        typeof player.years_from_peak === "number"
          ? player.years_from_peak
          : null;

      if (isPeak) {
        primeCounts.atPeak++;
      } else if (inPrime) {
        if (yearsFromPeak !== null && yearsFromPeak < 0) {
          primeCounts.inPrimePre++;
        } else {
          // Assume post-peak if in prime but not peak and not pre-peak
          primeCounts.inPrimePost++;
        }
      } else {
        // Not in prime
        if (yearsFromPeak !== null && yearsFromPeak < 0) {
          primeCounts.prePrime++;
        } else {
          // Assume post-prime if not in prime and not pre-prime
          primeCounts.postPrime++;
        }
      }
    });

    const total = totalValidPlayers || 1; // Use valid count, avoid division by zero

    const phases = [
      {
        name: "Pre-Prime",
        value: (primeCounts.prePrime / total) * 100,
        color: "#8884d8",
        count: primeCounts.prePrime,
        totalSample: totalValidPlayers,
      },
      {
        name: "In Prime (Pre-Peak)",
        value: (primeCounts.inPrimePre / total) * 100,
        color: "#82ca9d",
        count: primeCounts.inPrimePre,
        totalSample: totalValidPlayers,
      },
      {
        name: "At Peak",
        value: (primeCounts.atPeak / total) * 100,
        color: "#e1ca44",
        count: primeCounts.atPeak,
        totalSample: totalValidPlayers,
      },
      {
        name: "In Prime (Post-Peak)",
        value: (primeCounts.inPrimePost / total) * 100,
        color: "#ff7300",
        count: primeCounts.inPrimePost,
        totalSample: totalValidPlayers,
      },
      {
        name: "Post-Prime",
        value: (primeCounts.postPrime / total) * 100,
        color: "#9e9e9e",
        count: primeCounts.postPrime,
        totalSample: totalValidPlayers,
      },
    ];

    setCareerPhases(phases);
  }, []); // No dependencies needed inside useCallback if it only references its args

  // useEffect to process data when inputs change
  useEffect(() => {
    if (data) {
      const filteredData = getSafeFilteredData(
        data,
        selectedSports,
        selectedLeagues,
        selectedPositions
      );

      // Get aging curves by league for the main chart
      const curves = getAgingCurvesByLeague(filteredData);
      setAgingCurves(curves);

      // Calculate performance at selected age using the already filtered data
      calculateAgePerformance(selectedAge, filteredData);
    }
  }, [
    data,
    selectedAge,
    selectedSports,
    selectedLeagues,
    selectedPositions,
    calculateAgePerformance,
  ]); // Add calculateAgePerformance to dependency array

  // Filter valid curves based on having enough data points
  const validAgingCurves = useMemo(() => {
    return agingCurves.filter((curve) => {
      const hasEnoughData = curve.agingCurve.some((point) => point.count >= 10); // Lowered threshold slightly
      return hasEnoughData;
    });
  }, [agingCurves]);

  // Custom tooltip component to avoid DOM nesting issues
  const CustomTooltip = ({ active, payload, label }) => {
    if (!active || !payload || !payload.length) return null;

    const data = payload[0].payload;
    return (
      <div className="custom-tooltip">
        <div className="league-info">
          {useLogos ? (
            <LeagueDisplay
              league={payload[0].name}
              useLogos={true}
              size="medium"
            />
          ) : (
            <span>{payload[0].name}</span>
          )}
        </div>
        <div className="value-info">
          <span>Age: {label}</span>
          <span>Value: {payload[0].value.toFixed(3)}</span>
          <span>Players: {data.count}</span>
        </div>
      </div>
    );
  };

  return (
    <div className="aging-curves">
      <div className="section-header">
        <h2 style={{ display: "flex", alignItems: "center" }}>
          Aging Curves
          <ContextInfo
            title="Aging Curves Analysis"
            description="This analysis examines how athlete performance changes with age across different sports and leagues. It includes normalized performance curves, performance comparisons at specific ages, and career phase distribution data."
          />
        </h2>
      </div>

      <EnhancedFilters
        allData={data?.pqiData || []} // Pass pqiData for stable filter options
        sports={allSports}
        leagues={leagues} // Pass available leagues
        positions={allPositions}
        selectedSports={selectedSports}
        selectedLeagues={selectedLeagues}
        selectedPositions={selectedPositions}
        onSportsChange={setSelectedSports}
        onLeaguesChange={setSelectedLeagues}
        onPositionsChange={setSelectedPositions}
        showLimit={false}
        useLogos={useLogos}
      />

      <div className="age-selector-container">
        <h3 style={{ display: "flex", alignItems: "center" }}>
          Select Age to Explore: {selectedAge}
          <ContextInfo
            title="Age Selector"
            description="This slider allows you to select a specific age to analyze athlete performance and career phase distribution across different leagues."
          />
        </h3>
        <input
          type="range"
          min={18}
          max={40}
          value={selectedAge}
          onChange={(e) => setSelectedAge(parseInt(e.target.value))}
          className="age-slider"
        />
      </div>

      <div className="charts-container">
        {/* Row 1: Normalized Performance Curve */}
        <div className="charts-row">
          <div className="chart-container full-width">
            <h3 style={{ display: "flex", alignItems: "center" }}>
              Normalized Performance by Age
              <ContextInfo
                title="Normalized Performance by Age"
                description="This chart shows how athlete performance evolves with age across different leagues. Values are normalized to allow direct comparison. The x-axis represents age, while the y-axis shows relative performance value."
              />
            </h3>
            {validAgingCurves.length > 0 ? (
              <ResponsiveContainer width="100%" height={400}>
                <LineChart>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis
                    dataKey="age"
                    type="number"
                    domain={[18, 40]}
                    label={{
                      value: "Age",
                      position: "insideBottom",
                      offset: -5,
                      style: { textAnchor: "middle" },
                    }}
                  />
                  <YAxis
                    label={{
                      value: "Normalized Performance Value",
                      angle: -90,
                      position: "insideLeft",
                      style: { textAnchor: "middle" },
                    }}
                    yAxisId="default"
                  />
                  {/* Updated Tooltip to use custom component */}
                  <Tooltip content={<CustomTooltip />} />
                  <Legend
                    wrapperStyle={{ paddingTop: 10, paddingBottom: 10 }}
                    formatter={(value, entry) => {
                      return (
                        <LeagueDisplay
                          league={value}
                          useLogos={useLogos}
                          size="medium"
                        />
                      );
                    }}
                  />

                  {validAgingCurves.map((curve) => (
                    <Line
                      key={curve.league}
                      data={curve.agingCurve.filter(
                        (point) => point.count >= 10
                      )} // Apply threshold
                      type="monotone"
                      dataKey="avgValue"
                      name={curve.league}
                      stroke={leagueColorMapping[curve.league]}
                      dot={{ r: 2 }}
                      activeDot={{ r: 4 }}
                      yAxisId="default"
                      connectNulls // Connect line even if some points are missing
                    />
                  ))}
                </LineChart>
              </ResponsiveContainer>
            ) : (
              <div className="no-data">
                No aging curve data available for the selected filters.
              </div>
            )}
          </div>
        </div>

        {/* Row 2: Performance at Age Bar Chart and Career Phase Pie Chart */}
        <div className="charts-row">
          <div className="chart-container half-width">
            <h3 style={{ display: "flex", alignItems: "center" }}>
              Performance at Age {selectedAge}
              <ContextInfo
                title="Performance at Selected Age"
                description="This bar chart compares average performance values across different leagues at the selected age. This allows you to see which sports/leagues have better relative performance at this specific age point."
              />
            </h3>
            {agePerformance.length > 0 ? (
              <ResponsiveContainer width="100%" height={400}>
                <BarChart
                  data={agePerformance}
                  margin={{ top: 5, right: 30, bottom: 80, left: 30 }}
                >
                  {" "}
                  {/* Increased bottom margin */}
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis
                    dataKey="league"
                    tick={(props) => {
                      const { x, y, payload } = props;
                      return (
                        <g transform={`translate(${x},${y})`}>
                          {useLogos ? (
                            <foreignObject width="30" height="30" x="-10" y="5">
                              <LeagueDisplay
                                league={payload.value}
                                useLogos={true}
                                size="large"
                              />
                            </foreignObject>
                          ) : (
                            <text
                              x={0}
                              y={0}
                              dy={16}
                              textAnchor="end"
                              fill="#666"
                              transform="rotate(-45)"
                              fontSize="12"
                            >
                              {payload.value}
                            </text>
                          )}
                        </g>
                      );
                    }}
                    interval={0}
                    angle={-45}
                    textAnchor="end"
                    height={80} // Keep height for rotated labels
                  />
                  <YAxis
                    label={{
                      value: "Average Performance",
                      angle: -90,
                      position: "insideLeft",
                      style: { textAnchor: "middle" },
                      dx: -10,
                    }}
                  />
                  <Tooltip
                    content={({ active, payload, label }) => {
                      if (!active || !payload || !payload.length) return null;

                      return (
                        <div className="custom-tooltip">
                          <div className="league-info">
                            {useLogos ? (
                              <LeagueDisplay
                                league={label}
                                useLogos={true}
                                size="medium"
                              />
                            ) : (
                              <span>{label}</span>
                            )}
                          </div>
                          <div className="value-info">
                            <span>
                              Performance: {payload[0].value.toFixed(3)}
                            </span>
                          </div>
                        </div>
                      );
                    }}
                  />
                  {/* <Legend wrapperStyle={{ paddingTop: 15 }} /> */}{" "}
                  {/* Legend might be redundant */}
                  <Bar dataKey="avgValue" name="Performance">
                    {agePerformance.map((entry, index) => (
                      <Cell
                        key={`cell-${index}`}
                        fill={leagueColorMapping[entry.league]}
                      />
                    ))}
                  </Bar>
                </BarChart>
              </ResponsiveContainer>
            ) : (
              <div className="no-data">
                No performance data available for age {selectedAge}.
              </div>
            )}
          </div>

          <div className="chart-container half-width">
            {/* Pass selectedAge down to the Pie Chart */}
            <CareerPhasePieChart
              phaseData={careerPhases}
              useLogos={useLogos}
              selectedAge={selectedAge}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default AgingCurves;
