import React, { useState, useEffect } from "react";
import LeagueDisplay from "./LeagueDisplay";
import SportDisplay from "./SportDisplay";
import PositionDisplay from "./PositionDisplay"; // Import PositionDisplay
import "../styles/EnhancedFilters.css";

const MultiSelect = ({
  id,
  label,
  options,
  value,
  onChange,
  disabled = false,
  useLogos = false, // Add useLogos prop
  optionType = "text", // Add optionType prop: "text", "league", "sport", or "position"
  allData = [], // New prop to access the full dataset for context
  selectedLeagues = [], // To provide league context for positions
  selectedSports = [], // To provide sport context for positions
}) => {
  const [isOpen, setIsOpen] = useState(false);

  const handleOptionClick = (option) => {
    // If the option is already selected, remove it
    if (value.includes(option)) {
      onChange(value.filter((item) => item !== option));
    } else {
      // Otherwise, add it
      onChange([...value, option]);
    }
  };

  // Helper function to find the most common league for a position
  const findContextForPosition = (position) => {
    // Default context
    let defaultContext = { league: "", sport: "" };

    // If we don't have allData, we can't determine context
    if (!allData || allData.length === 0) return defaultContext;

    // Find all items with this position
    const positionItems = allData.filter((item) => item.position === position);
    if (positionItems.length === 0) return defaultContext;

    // If leagues are selected, prioritize those leagues
    if (selectedLeagues.length > 0) {
      const filteredByLeague = positionItems.filter((item) =>
        selectedLeagues.includes(item.league)
      );
      if (filteredByLeague.length > 0) {
        // Use the most common league for this position
        const leagueCounts = {};
        filteredByLeague.forEach((item) => {
          leagueCounts[item.league] = (leagueCounts[item.league] || 0) + 1;
        });
        const mostCommonLeague = Object.keys(leagueCounts).reduce((a, b) =>
          leagueCounts[a] > leagueCounts[b] ? a : b
        );

        // Find a sport for this league
        const sport =
          filteredByLeague.find((item) => item.league === mostCommonLeague)
            ?.sport || "";
        return { league: mostCommonLeague, sport };
      }
    }

    // If sports are selected, prioritize those sports
    if (selectedSports.length > 0) {
      const filteredBySport = positionItems.filter((item) =>
        selectedSports.includes(item.sport)
      );
      if (filteredBySport.length > 0) {
        // Use the most common sport and its league
        const sportCounts = {};
        filteredBySport.forEach((item) => {
          sportCounts[item.sport] = (sportCounts[item.sport] || 0) + 1;
        });
        const mostCommonSport = Object.keys(sportCounts).reduce((a, b) =>
          sportCounts[a] > sportCounts[b] ? a : b
        );

        // Find a league for this sport
        const sampleItem = filteredBySport.find(
          (item) => item.sport === mostCommonSport
        );
        return { league: sampleItem?.league || "", sport: mostCommonSport };
      }
    }

    // If no filters applied or no matches, use the most common context overall
    const counts = {};
    positionItems.forEach((item) => {
      const key = `${item.league}|${item.sport}`;
      counts[key] = (counts[key] || 0) + 1;
    });

    // Find the most common context
    let mostCommonKey = Object.keys(counts)[0] || "";
    Object.keys(counts).forEach((key) => {
      if (counts[key] > counts[mostCommonKey]) {
        mostCommonKey = key;
      }
    });

    // Parse the key back into league and sport
    const [league, sport] = mostCommonKey.split("|");
    return { league, sport };
  };

  // Helper function to render the option content based on type
  const renderOptionContent = (option) => {
    if (optionType === "league") {
      return (
        <div className="option-with-logo">
          <LeagueDisplay league={option} useLogos={useLogos} size="small" />
        </div>
      );
    } else if (optionType === "sport") {
      return (
        <div className="option-with-logo">
          <SportDisplay sport={option} useLogos={useLogos} size="small" />
        </div>
      );
    } else if (optionType === "position") {
      // Find appropriate context for this position
      const { league, sport } = findContextForPosition(option);

      // Now with proper context
      return (
        <div className="option-with-logo">
          <PositionDisplay
            position={option}
            league={league}
            sport={sport}
            showFull={true}
            size="small"
          />
        </div>
      );
    } else {
      // Default text display
      return option;
    }
  };

  // For selected values display in the input
  const renderSelectedDisplay = () => {
    if (value.length === 0) {
      return <span className="placeholder">Select {label}</span>;
    } else if (value.length === 1) {
      return (
        <span className="selected-values">{renderOptionContent(value[0])}</span>
      );
    } else {
      return (
        <span className="selected-values">{value.length} items selected</span>
      );
    }
  };

  return (
    <div className="multi-select-container">
      <label htmlFor={id}>{label}:</label>
      <div
        className={`multi-select-input ${disabled ? "disabled" : ""}`}
        onClick={() => !disabled && setIsOpen(!isOpen)}
      >
        {renderSelectedDisplay()}
        <span className="dropdown-arrow">▼</span>
      </div>

      {isOpen && !disabled && (
        <div className="multi-select-dropdown">
          <div className="select-all-option" onClick={() => onChange([])}>
            Clear All
          </div>
          <div
            className="select-all-option"
            onClick={() => onChange([...options])}
          >
            Select All
          </div>
          {options.map((option) => (
            <div
              key={option}
              className={`multi-select-option ${
                value.includes(option) ? "selected" : ""
              }`}
              onClick={() => handleOptionClick(option)}
            >
              <input
                type="checkbox"
                checked={value.includes(option)}
                onChange={() => {}} // Handled by div click
                id={`${id}-${option}`}
              />
              <label htmlFor={`${id}-${option}`}>
                {renderOptionContent(option)}
              </label>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

// Create a custom select for limit
const LimitSelect = ({ value, onChange }) => {
  const options = [
    { value: 0, label: "None (All)" },
    { value: 10, label: "10" },
    { value: 25, label: "25" },
    { value: 50, label: "50" },
    { value: 100, label: "100" },
    { value: 500, label: "500" },
    { value: 1000, label: "1000" },
    { value: 2000, label: "2000" },
    { value: 3000, label: "3000" },
  ];

  return (
    <div className="filter-item">
      <label>Limit:</label>
      <select
        value={value}
        onChange={(e) => onChange(parseInt(e.target.value))}
      >
        {options.map((option) => (
          <option key={option.value} value={option.value}>
            {option.label}
          </option>
        ))}
      </select>
    </div>
  );
};

// Main enhanced filters component to be used across the app
const EnhancedFilters = ({
  allData = [], // Provide default empty array
  sports = [],
  leagues = [],
  positions = [],
  selectedSports = [],
  selectedLeagues = [],
  selectedPositions = [],
  limit = 25,
  onSportsChange,
  onLeaguesChange,
  onPositionsChange,
  onLimitChange,
  showLimit = true,
  useLogos = false, // Add useLogos prop
}) => {
  const [filteredLeagues, setFilteredLeagues] = useState(leagues);
  const [filteredPositions, setFilteredPositions] = useState(positions);

  // FIX: Keep all leagues visible but apply filtering logic to positions
  // Keep all leagues visible regardless of sport selection
  useEffect(() => {
    setFilteredLeagues(leagues);
  }, [leagues]);

  // Filter positions based on selected leagues and sports
  useEffect(() => {
    if (!Array.isArray(allData) || allData.length === 0) {
      setFilteredPositions(positions);
      return;
    }

    let positionsToShow = positions;

    // If sports are selected, filter by sports
    if (selectedSports.length > 0) {
      const filteredData = allData.filter(
        (item) => item && selectedSports.includes(item.sport)
      );

      // If leagues are also selected, further filter
      if (selectedLeagues.length > 0) {
        const filteredBySportAndLeague = filteredData.filter((item) =>
          selectedLeagues.includes(item.league)
        );

        positionsToShow = [
          ...new Set(
            filteredBySportAndLeague
              .map((item) => item.position)
              .filter(Boolean)
          ),
        ];
      } else {
        positionsToShow = [
          ...new Set(filteredData.map((item) => item.position).filter(Boolean)),
        ];
      }
    } else if (selectedLeagues.length > 0) {
      // Only leagues are selected
      const filteredByLeague = allData.filter(
        (item) => item && selectedLeagues.includes(item.league)
      );

      positionsToShow = [
        ...new Set(
          filteredByLeague.map((item) => item.position).filter(Boolean)
        ),
      ];
    }

    setFilteredPositions(positionsToShow);
  }, [selectedSports, selectedLeagues, positions, allData]);

  return (
    <div className="filters enhanced-filters">
      <MultiSelect
        id="sports-select"
        label="Sport"
        options={sports}
        value={selectedSports}
        onChange={onSportsChange}
        optionType="sport" // Specify this is for sports
        useLogos={useLogos} // Pass useLogos prop
        allData={allData}
        selectedLeagues={selectedLeagues}
        selectedSports={selectedSports}
      />

      <MultiSelect
        id="leagues-select"
        label="League"
        options={leagues}
        value={selectedLeagues.filter((l) => leagues.includes(l))}
        onChange={onLeaguesChange}
        disabled={filteredLeagues.length === 0}
        optionType="league" // Specify this is for leagues
        useLogos={useLogos} // Pass useLogos prop
        allData={allData}
        selectedLeagues={selectedLeagues}
        selectedSports={selectedSports}
      />

      <MultiSelect
        id="positions-select"
        label="Position"
        options={filteredPositions}
        value={selectedPositions.filter((p) => filteredPositions.includes(p))}
        onChange={onPositionsChange}
        disabled={filteredPositions.length === 0}
        optionType="position" // Specify this is for positions
        useLogos={useLogos}
        allData={allData} // Pass the full dataset
        selectedLeagues={selectedLeagues} // Pass selected leagues for context
        selectedSports={selectedSports} // Pass selected sports for context
      />

      {showLimit && <LimitSelect value={limit} onChange={onLimitChange} />}
    </div>
  );
};

export default EnhancedFilters;
