// src/components/PrimeIdentifier.js
import React, { useState, useEffect, useRef, useCallback } from "react";
import { leagueColorMapping } from "../utils/constants";
import ContextInfo from "./ContextInfo";
import LeagueDisplay from "./LeagueDisplay";
import SportDisplay from "./SportDisplay";
import _ from "lodash";
import toast, { Toaster } from "react-hot-toast";
import ReactGA from "react-ga4";
import "../styles/PrimeIdentifier.css";

// --- Import Utility Functions ---
import { fetchDataFromApi } from "../utils/api"; // *** IMPORTED ***
import { processApiData } from "../utils/dataProcessing"; // *** IMPORTED ***

// --- Constants (Keep as is) ---
const DEFAULT_THRESHOLD_PCT = 70;
// const DEFAULT_GAMES_PCT_THRESHOLD = 100;
const MAX_CUSTOM_DEFINITIONS = 5;
const ORIGINAL_DEFINITION_ID = "original_data_definition";
// API_ENDPOINT is now handled by api.js, but keep ESTIMATED_API_TIME
const ESTIMATED_API_TIME = 70;

// --- Utility Functions (Keep as is) ---
const getParamsKey = (params) => `t${params.thresholdPct}`;
// const getParamsKey = (params) => `t${params.thresholdPct}_g${params.gamesPctThreshold}`;

const calculateLeaguePrimeStats = (primes) => {
  if (!Array.isArray(primes) || primes.length === 0) return [];
  const leagueGroups = _.groupBy(primes, "league");
  const stats = Object.entries(leagueGroups)
    .map(([league, playersInLeague]) => {
      if (!playersInLeague || playersInLeague.length === 0) return null;
      const validDurations = playersInLeague
        .map((p) => p?.prime_duration)
        .filter((d) => typeof d === "number" && isFinite(d));
      const avgDuration =
        validDurations.length > 0 ? _.mean(validDurations) : 0;
      const validStartAges = playersInLeague
        .map((p) => p?.start_age)
        .filter((a) => typeof a === "number" && isFinite(a));
      const avgStartAge =
        validStartAges.length > 0 ? _.mean(validStartAges) : 0;
      const validEndAges = playersInLeague
        .map((p) => p?.end_age)
        .filter((a) => typeof a === "number" && isFinite(a));
      const avgEndAge = validEndAges.length > 0 ? _.mean(validEndAges) : 0;
      const sport = playersInLeague.find((p) => p.sport)?.sport || "";
      return {
        league,
        sport,
        avgDuration,
        avgStartAge,
        avgEndAge,
        playerCount: playersInLeague.length,
        color: leagueColorMapping[league] || "#cccccc",
      };
    })
    .filter(Boolean);
  return _.orderBy(stats, ["playerCount", "avgDuration"], ["desc", "desc"]);
};

// --- Main Component (Accepting timer props) ---
const PrimeIdentifier = ({
  originalData,
  availableDefinitions: globalAvailableDefinitions,
  activeDefinitionKey: globalActiveDefinitionKey,
  onPrimeChange,
  onDefinitionSelect,
  getColorForDefinition,
  onLoadingStart = () => {},
  onLoadingEnd = () => {},
  isApiLoading: globalIsApiLoading,
  useLogos = false,
  simplifiedView = false,
}) => {
  // Local state for sliders
  const [thresholdPct, setThresholdPct] = useState(DEFAULT_THRESHOLD_PCT);
  // const [gamesPctThreshold, setGamesPctThreshold] = useState(DEFAULT_GAMES_PCT_THRESHOLD);

  // State for API errors specific to calculations triggered here
  const [apiErrors, setApiErrors] = useState({});

  // State for the data currently being displayed (derived from global state)
  const [displayData, setDisplayData] = useState({
    rawPrimes: [],
    splinePrimes: [],
    pqi: [],
    cqi: [],
    fullData: [],
    leagueStats: [],
    label: "N/A",
  });

  // --- Effects ---
  // Sync local display and slider values with global state changes (Keep as is)
  useEffect(() => {
    const currentGlobalDef = globalAvailableDefinitions?.find(
      (def) => def.id === globalActiveDefinitionKey
    );
    if (currentGlobalDef) {
      let sourceData =
        currentGlobalDef.id === ORIGINAL_DEFINITION_ID
          ? originalData
          : currentGlobalDef.primeData || originalData;

      if (sourceData && sourceData.primesSplineData) {
        const currentLeagueStats = calculateLeaguePrimeStats(
          sourceData.primesSplineData
        );
        setDisplayData({
          rawPrimes: sourceData.primesRawData || [],
          splinePrimes: sourceData.primesSplineData || [],
          pqi: sourceData.pqiData || [],
          cqi: sourceData.cqiData || [],
          fullData: sourceData.fullData || [],
          leagueStats: currentLeagueStats,
          label: currentGlobalDef.label || "Unnamed Definition",
        });
      } else {
        console.warn(
          "PrimeIdentifier: Invalid source data for definition:",
          globalActiveDefinitionKey,
          sourceData
        );
        setDisplayData({
          rawPrimes: [],
          splinePrimes: [],
          pqi: [],
          cqi: [],
          fullData: [],
          leagueStats: [],
          label: "Error: Invalid Data",
        });
      }

      if (
        currentGlobalDef.parameters &&
        currentGlobalDef.id !== ORIGINAL_DEFINITION_ID
      ) {
        setThresholdPct(
          currentGlobalDef.parameters.thresholdPct ?? DEFAULT_THRESHOLD_PCT
        );
        // setGamesPctThreshold(currentGlobalDef.parameters.gamesPctThreshold ?? DEFAULT_GAMES_PCT_THRESHOLD);
      } else if (currentGlobalDef.id === ORIGINAL_DEFINITION_ID) {
        setThresholdPct(DEFAULT_THRESHOLD_PCT);
        // setGamesPctThreshold(DEFAULT_GAMES_PCT_THRESHOLD);
      }
    } else {
      setDisplayData({
        rawPrimes: [],
        splinePrimes: [],
        pqi: [],
        cqi: [],
        fullData: [],
        leagueStats: [],
        label: "N/A",
      });
    }
  }, [globalActiveDefinitionKey, globalAvailableDefinitions, originalData]);

  // --- Event Handlers ---
  const handleParameterChange = (setter) => (event) => {
    setter(parseInt(event.target.value, 10));
  };

  // --- *** UPDATED handleCalculateRequest *** ---
  const handleCalculateRequest = useCallback(async () => {
    const currentParams = { thresholdPct }; // Use only thresholdPct for key/label
    // const currentParams = { thresholdPct, gamesPctThreshold }; // If using games
    const paramsKey = getParamsKey(currentParams); // Key based on input params
    const apiPayload = { thresholdPct, gamesPctThreshold: 100 }; // Payload for API - ** ADJUST gamesPctThreshold IF USING SLIDER **

    ReactGA.event({
      category: "Feature Usage",
      action: "Used Prime Calculator",
      label: `Threshold: ${thresholdPct}%`,
      value: thresholdPct,
    });

    if (globalIsApiLoading) {
      toast.error("A calculation is already in progress.", { duration: 3000 });
      return;
    }

    const existingGlobalDef = globalAvailableDefinitions?.find(
      (def) => def.id === paramsKey
    );
    if (existingGlobalDef) {
      toast.success(
        `Definition exists: ${existingGlobalDef.label}. Switching view.`,
        { duration: 4000 }
      );
      if (onDefinitionSelect) onDefinitionSelect(paramsKey);
      return;
    }

    const customDefCount =
      globalAvailableDefinitions?.filter(
        (def) => def.id !== ORIGINAL_DEFINITION_ID
      ).length || 0;
    if (customDefCount >= MAX_CUSTOM_DEFINITIONS) {
      toast.error(
        `Maximum of ${MAX_CUSTOM_DEFINITIONS} custom definitions reached.`
      );
      return;
    }

    setApiErrors((prev) => {
      const n = { ...prev };
      delete n[paramsKey];
      return n;
    });
    onLoadingStart(ESTIMATED_API_TIME); // Call Dashboard's start timer
    const toastId = toast.loading(
      `Calculating: T=${thresholdPct}%... (Est. ${ESTIMATED_API_TIME}s)`
    );

    try {
      // --- Use fetchDataFromApi ---
      const apiConfig = { endpoint: "/recalculate", params: apiPayload };
      const rawApiResponse = await fetchDataFromApi(apiConfig); // Pass config

      // --- Use processApiData ---
      // Pass the raw response and the generated key to the processor
      const processedData = processApiData(rawApiResponse, paramsKey);

      // Note: processApiData throws error if essential data is missing, so we assume structure is good here
      // Construct the label based on the ACTUAL parameters returned by processApiData (safer)
      const returnedParams =
        processedData?.pqiData?.[0]?.parameters || // Attempt to get params from PQI data
        processedData?.cqiData?.[0]?.parameters || // Or CQI data
        apiPayload; // Fallback to sent params
      const finalLabel = `Custom: T=${returnedParams.thresholdPct}%`;
      // const finalLabel = `Custom: T=${returnedParams.thresholdPct}% / G=${returnedParams.gamesPctThreshold}%`; // If using games

      // Package the PROCESSED data for Dashboard's handlePrimeChange
      const newDefinitionPayload = {
        definitionKey: paramsKey, // The key corresponding to the input parameters
        definitionLabel: finalLabel, // Label based on processed/returned params
        definitionParams: returnedParams, // Store actual params used/returned
        // Send the FULL processed data structure
        primeData: processedData, // This now contains the CORRECTLY PROCESSED data object
        isOriginal: false,
        // Deprecated individual keys (primeData contains everything)
        // raw: processedData.primesRawData,
        // spline: processedData.primesSplineData,
        // pqi: processedData.pqiData,
        // cqi: processedData.cqiData,
        // fullData: processedData.fullData,
      };

      toast.success(`Calculation complete for ${finalLabel}`, { id: toastId });
      if (onPrimeChange) onPrimeChange(newDefinitionPayload); // Send processed data structure UP
    } catch (error) {
      console.error(`API Call/Processing failed for ${paramsKey}:`, error);
      // Store the specific error against the key attempt
      setApiErrors((prev) => ({ ...prev, [paramsKey]: error.message }));
      toast.error(`Calculation failed: ${error.message}`, {
        id: toastId,
        duration: 6000,
      });
    } finally {
      onLoadingEnd(); // Call Dashboard's end timer
    }
  }, [
    thresholdPct,
    // gamesPctThreshold, // If using
    globalIsApiLoading,
    globalAvailableDefinitions,
    onPrimeChange,
    onDefinitionSelect,
    onLoadingStart,
    onLoadingEnd,
  ]);

  const handleSelectDefinition = (event) => {
    const selectedKey = event.target.value;
    if (
      selectedKey !== globalActiveDefinitionKey &&
      !globalIsApiLoading &&
      onDefinitionSelect
    ) {
      onDefinitionSelect(selectedKey);
      setApiErrors((prev) => {
        const n = { ...prev };
        delete n[selectedKey];
        return n;
      });
    }
  };

  // --- Derived State for Rendering (Keep as is) ---
  const customDefCount =
    globalAvailableDefinitions?.filter(
      (def) => def.id !== ORIGINAL_DEFINITION_ID
    ).length || 0;
  const canCalculate =
    !globalIsApiLoading && customDefCount < MAX_CUSTOM_DEFINITIONS;
  const currentParamsKey = getParamsKey({ thresholdPct });
  const isCurrentParamAvailable = !!globalAvailableDefinitions?.some(
    (def) => def.id === currentParamsKey
  );
  const calculateButtonTooltip = globalIsApiLoading
    ? `Calculation in progress...`
    : isCurrentParamAvailable
    ? `Definition with T=${thresholdPct}% already exists. Select it from the dropdown.`
    : customDefCount >= MAX_CUSTOM_DEFINITIONS
    ? `Maximum of ${MAX_CUSTOM_DEFINITIONS} custom definitions reached.`
    : `Calculate new data with T=${thresholdPct}%. (Est. ${ESTIMATED_API_TIME}s)`;

  const definitionOptions =
    globalAvailableDefinitions
      ?.sort((a, b) =>
        a.id === ORIGINAL_DEFINITION_ID
          ? -1
          : b.id === ORIGINAL_DEFINITION_ID
          ? 1
          : 0
      )
      .map((def) => ({
        value: def.id,
        label: `${def.label}${apiErrors[def.id] ? " (Error!)" : ""}`, // Show local calc error
        error: apiErrors[def.id] || null,
      })) || [];

  const avgSplineDuration = (
    displayData.splinePrimes?.reduce(
      (sum, p) => sum + (Number(p.prime_duration) || 0),
      0
    ) / (displayData.splinePrimes?.length || 1)
  ).toFixed(1);
  const splineCount = displayData.splinePrimes?.length || 0;

  // --- Render (Keep structure as is, uses updated state/props) ---
  return (
    <div className="prime-identifier component-box">
      <Toaster position="top-right" containerStyle={{ zIndex: 9999 }} />
      <div className="section-header">
        <h2>Prime Definition Explorer</h2>
        {displayData.label !== "N/A" && (
          <span
            className={`status-indicator ${
              globalActiveDefinitionKey === ORIGINAL_DEFINITION_ID
                ? "original"
                : "modified"
            }`}
          >
            {" "}
            Viewing: {displayData.label}{" "}
          </span>
        )}
        {apiErrors[globalActiveDefinitionKey] && (
          <div className="api-error-message inline-error">
            {" "}
            Error calculating this definition:{" "}
            {apiErrors[globalActiveDefinitionKey]}{" "}
          </div>
        )}
      </div>
      <div className="settings-panel">
        <div className="threshold-controls control-group">
          <div className="slider-container">
            <label htmlFor="threshold-pct">
              Performance Threshold (%): {thresholdPct}%
            </label>
            <div className="slider-with-labels">
              <div className="slider-label left green">
                More Flexible Prime ←
              </div>
              <input
                type="range"
                id="threshold-pct"
                min="0"
                max="100"
                step="5"
                value={thresholdPct}
                onChange={handleParameterChange(setThresholdPct)}
                disabled={globalIsApiLoading}
              />
              <div className="slider-label right red">→ Stricter Prime</div>
            </div>
          </div>
        </div>
        <div className="definition-management">
          <div className="control-group definition-selector">
            <label htmlFor="definition-select">View Definition:</label>
            <select
              id="definition-select"
              value={globalActiveDefinitionKey}
              onChange={handleSelectDefinition}
              disabled={globalIsApiLoading}
            >
              {definitionOptions.map((opt) => (
                <option
                  key={opt.value}
                  value={opt.value}
                  title={opt.error || ""}
                >
                  {" "}
                  {opt.label}{" "}
                </option>
              ))}
            </select>
            {apiErrors[globalActiveDefinitionKey] && (
              <span
                className="error-text"
                style={{ marginLeft: "5px", color: "red" }}
              >
                {" "}
                (Load Error!)
              </span>
            )}
          </div>
          <div className="action-buttons">
            <button
              onClick={handleCalculateRequest}
              disabled={
                !canCalculate || isCurrentParamAvailable || globalIsApiLoading
              }
              className={`calculate-button ${
                globalIsApiLoading ? "loading" : ""
              }`}
              title={calculateButtonTooltip}
            >
              {globalIsApiLoading ? `Calculating...` : "Calculate & Add"}
            </button>
          </div>
        </div>
      </div>
      <div className="global-stats-summary component-box">
        <h4>Overall Stats (Viewing: {displayData.label})</h4>
        <div className="stat-cards">
          <div className="stat-card">
            Players (Spline Primes): <span>{splineCount.toLocaleString()}</span>
          </div>
          <div className="stat-card">
            Avg. Prime Duration (Spline):{" "}
            <span>
              {avgSplineDuration === "NaN" ? "N/A" : `${avgSplineDuration} yrs`}
            </span>
          </div>
        </div>
      </div>
      {displayData.leagueStats.length > 0 && (
        <div className="league-prime-comparison component-box">
          <h4>League Comparison</h4>
          <table>
            <thead>
              <tr>
                <th>League</th>
                <th>Players</th>
                <th>Avg Start Age</th>
                <th>Avg End Age</th>
                <th>Avg Duration</th>
              </tr>
            </thead>
            <tbody>
              {displayData.leagueStats.map((lg) => (
                <tr key={lg.league}>
                  <td>
                    <LeagueDisplay
                      league={lg.league}
                      useLogos={useLogos}
                      size="large"
                      bold={true}
                      showBackground={!useLogos}
                    />
                  </td>
                  <td>{lg.playerCount.toLocaleString()}</td>
                  <td>{lg.avgStartAge?.toFixed(1) ?? "N/A"}</td>
                  <td>{lg.avgEndAge?.toFixed(1) ?? "N/A"}</td>
                  <td>{lg.avgDuration?.toFixed(1) ?? "N/A"}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
      {globalAvailableDefinitions && globalAvailableDefinitions.length > 1 && (
        <div className="definitions-comparison component-box">
          <h4>Cross-Definition Comparison</h4>
          <table className="comparison-table">
            <thead>
              <tr>
                <th>Definition</th>
                <th>Players</th>
                <th>Avg Start Age</th>
                <th>Avg End Age</th>
                <th>Avg Duration</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {globalAvailableDefinitions.map((def) => {
                const dataSource =
                  def.id === ORIGINAL_DEFINITION_ID
                    ? originalData
                    : def.primeData;
                const primesToAnalyze = dataSource?.primesSplineData || [];
                const totalPlayers = primesToAnalyze.length;
                const avgStart =
                  totalPlayers > 0
                    ? _.meanBy(
                        primesToAnalyze.filter((p) => p.start_age != null),
                        "start_age"
                      )?.toFixed(1) ?? "N/A"
                    : "N/A";
                const avgEnd =
                  totalPlayers > 0
                    ? _.meanBy(
                        primesToAnalyze.filter((p) => p.end_age != null),
                        "end_age"
                      )?.toFixed(1) ?? "N/A"
                    : "N/A";
                const avgDur =
                  totalPlayers > 0
                    ? _.meanBy(
                        primesToAnalyze.filter((p) => p.prime_duration != null),
                        "prime_duration"
                      )?.toFixed(1) ?? "N/A"
                    : "N/A";
                return (
                  <tr
                    key={def.id}
                    className={
                      def.id === globalActiveDefinitionKey ? "active-row" : ""
                    }
                  >
                    <td>{def.label}</td>
                    <td>
                      {totalPlayers > 0 ? totalPlayers.toLocaleString() : "N/A"}
                    </td>
                    <td>{avgStart}</td>
                    <td>{avgEnd}</td>
                    <td>{avgDur}</td>
                    <td>
                      <button
                        onClick={() =>
                          onDefinitionSelect && onDefinitionSelect(def.id)
                        }
                        className="view-button"
                        disabled={
                          def.id === globalActiveDefinitionKey ||
                          globalIsApiLoading
                        }
                      >
                        {" "}
                        View{" "}
                      </button>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
};

export default PrimeIdentifier;
